<script lang="ts">
    import {createEventDispatcher, onMount} from "svelte";

    export let width = 600;
    export let height = 500;

    export let closeOnFocusOut = false;

    export let visible = true;

    const eventDispatcher = createEventDispatcher();


    const onFocusOut = () => {
        if (closeOnFocusOut) {
            eventDispatcher("close");
        }
    }

    onMount(() => {
    });

</script>

{#if visible}
    <div class="modal-container" on:keypress={onFocusOut} on:click="{onFocusOut}">
        <div class="modal"
             style:height="{height}px"
             style:width="{width}px"
             on:keypress={(e) => e.stopPropagation()}
             on:click={(e) => e.stopPropagation()}>
            <div class="modal-header">
                <slot name="header"></slot>
            </div>
            <div class="modal-content">
                <slot name="content"></slot>
            </div>
            <div class="modal-footer">
                <slot name="footer"></slot>
            </div>
        </div>
    </div>
{/if}

<style>
    .modal-container {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 99999998;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .5);
        color: black;
        overflow: hidden;
    }

    .modal {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        background-color: white;
        border-radius: 16px;

        display: flex;
        flex-direction: column;
    }

    .modal-header {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 16px;
        height: 60px;
        font-size: 32px;
    }

    .modal-content {
        padding: 16px;
        height: 70%;
        font-size: 18px;
    }

    .modal-footer {
        margin-top: auto;
        margin-bottom: 20px;
        display: flex;
        justify-content: center;
        height: 30px;
    }

    .modal-container :global(*::-webkit-scrollbar-thumb) {
        background-color: black; /* color of the scroll thumb */
        border-radius: 1px; /* roundness of the scroll thumb */
        /* border: 1px solid #000066;  *//* creates padding around scroll thumb */
    }
</style>
