<script lang="ts">
    import {onMount} from "svelte";
    import Button from "src/components/Button.svelte";
    import type {CardModel} from "src/models/card.model";
    import Card from "src/components/Card.svelte";
    import QuitModal from "src/components/Modals/QuitModal.svelte";
    import type {SideInfoData} from "src/models/side.model";
    import {sendNui} from "src/utils/fetchNui";
    import {spectator, waiting} from "src/store/game.store";
    import reset from "src/utils/reset";

    export let info: SideInfoData;
    export let leader: CardModel;

    export let isPlayer = false;

    let quitModelVisible = false;

    const onActivateLeader = () => {
        if ($spectator) return;
        if (!isPlayer) return;
        if (leader.disabled) return;

        console.log('activating leader');

        sendNui('activate:leader');
    }

    const onQuit = (quit: boolean) => {
        quitModelVisible = false;

        console.log('quitting. Is Spectator: ', $spectator, ' Quit: ', quit);
        if (quit) {
            sendNui('request:leaveRoom');
            sendNui('close');
            reset();
        }
    }

    const onPass = () => {
        if ($spectator) return;

        console.log('passing');
        sendNui('set:passing');
    }

    onMount(() => {
    });

</script>

<div class="profile">
    <h2 class="player-name">{info.name}</h2>
    <div class="profile-info">
        {#if leader}
            <Card card={leader} highlight={!$spectator && isPlayer && !$waiting && !leader?.disabled} on:click={onActivateLeader}/>
        {/if}
        <div class="profile-info-stats">
            <div class="profile-info-stat">
                <span>Points</span>
                <span>{info.score}</span>
            </div>
            <div class="profile-info-stat">
                <span>Hand</span>
                <span>{info.hand}</span>
            </div>
            <div class="profile-info-stat health">
                {#each Array(info.lives) as _}
                    <img src="assets/ruby.png" alt="heart"/>
                {/each}
                {#each Array(2 - info.lives) as _}
                    <img src="assets/ruby-grey.png" alt="heart"/>
                {/each}
            </div>
        </div>
    </div>
    {#if isPlayer}
        <div class="profile-actions">
            {#if !$waiting && !$spectator}
                <Button title="Pass" on:click={onPass}/>
            {/if}

            {#if !$spectator}
                <Button title="Give Up" on:click={() => quitModelVisible = true}/>
            {:else}
                <Button title="Quit" on:click={() => onQuit(true)}/>
            {/if}
        </div>
    {/if}
</div>

<QuitModal onQuit={onQuit} visible={quitModelVisible}/>

<style lang="scss">
  .profile {
    background-image: url("/assets/ui/bgOutline2.png");
    background-size: 230px 230px;
    background-repeat: no-repeat;
    background-position: center;

    display: flex;
    flex-direction: column;
    align-items: center;

    box-sizing: border-box;
    width: 230px;
    height: 230px;

    padding: 0 8px;
  }

  .player-name {
    margin: 8px 0 0;
    font-size: 1.4rem;
    font-weight: 500;
    text-align: center;
  }

  .profile-info, .profile-actions {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
  }

  .profile-info {
    margin-top: 16px;
  }

  .profile-info :global(.card) {
    height: 100px;
  }

  .profile-actions {
    gap: 8px;
    margin: auto auto 16px;
    padding-right: 24px;
    display: flex;
    justify-content: flex-end;
    box-sizing: border-box;
  }

  .profile-info-stats {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .profile-info-stat {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 30px;
    padding: 0 5px;
    box-sizing: border-box;
  }

  .profile-info-stat.health {
    justify-content: center;
    gap: 12px;

    img {
      width: 30px;
    }
  }

  .profile-actions :global(.cardgame-btn) {
    width: 40%;
    height: 30px;
  }

</style>
