<script lang="ts">
    import {onMount} from "svelte";
    import type {RangeDistance} from "src/models/field.model";
    import Card from "src/components/Card.svelte";
    import type {SideFieldData} from "src/models/side.model";
    import type {CardModel} from "src/models/card.model";
    import {setAgile, setHorn, spectator, waitForDecoy} from "src/store/game.store";
    import {sendNui} from "src/utils/fetchNui";

    type RangeData = {
        img: string;
        weatherCard: string;
    }

    const rangeDataList: { [key: string]: RangeData } = {
        close: {
            img: "assets/close-combat.png",
            weatherCard: "card_bitingfrost",
        },
        ranged: {
            img: "assets/ranged.png",
            weatherCard: "card_impenetrablefog",
        },
        siege: {
            img: "assets/siege.png",
            weatherCard: "card_torrentialrain",
        },
    }

    export let isPlayer: boolean;

    export let range: RangeDistance;

    export let field: SideFieldData = {
        cards: [],
        score: 0,
        horn: undefined,
    };
    export let weatherCards: Array<CardModel> = [];

    let isInfluencedByWeather = false;
    let isActive = false;

    $: {
        isInfluencedByWeather = weatherCards.some(card => card.key === rangeDataList[range].weatherCard);
        isActive = range === "siege" ? (!!$waitForDecoy || !!$setHorn) : (!!$waitForDecoy || !!$setAgile || !!$setHorn);
    }

    const onFieldCLick = (card?: CardModel) => {
        if ($spectator) return;
        if (!isPlayer) return;

        console.log("Field click.", "Range:", range, "card ?:", card?.key)

        if ($waitForDecoy) {
            console.log("Decoy: replacing with", card?.key)
            sendNui('decoy:replaceWith', {
                cardID: card?.id,
            })
            waitForDecoy.set(undefined);
            return;
        }

        if ($setAgile) {
            console.log("Agile: setting to", range)
            sendNui('agile:field', {
                field: range == "close" ? 0 : 1,
            })
            setAgile.set(undefined);
            return;
        }

        if ($setHorn) {
            console.log("Horn: setting to", range)
            sendNui('horn:field', {
                field: range == "close" ? 0 : (range == "ranged" ? 1 : 2),
            })
            setHorn.set(undefined);
            return;
        }
    }

    onMount(() => {
    });

</script>

<div class="field-range {range}" class:active={!$spectator && isPlayer && isActive} class:field-weather={isInfluencedByWeather}
     on:click={() => onFieldCLick()} on:keypress={() => onFieldCLick()}>
    <div class="horn">
        <img alt={range} src={rangeDataList[range].img}/>
        {#if field.horn}
            <Card card={field.horn} />
        {/if}
    </div>
    <div class="card-list">
        {#each field.cards as card}
            <Card card={card} highlight={false}
                  on:click={() => onFieldCLick(card)}
                  on:keypress={() => onFieldCLick(card)}
            />
        {/each}
    </div>
    <div class="counter">
        {field.score}
    </div>
</div>

<style lang="scss">
  .field-range {
    display: flex;
    flex: 1;
    color: white;
    border: 1px solid white;
  }

  .field-range {
    .horn, .counter {
      font-size: 1.5rem;
      text-align: center;

      display: flex;
      justify-content: center;
      align-items: center;

      width: 10%;
      height: 100%;
      border-right: 1px solid white;
    }

    .horn {
      position: relative;

      :global(.card) {
        height: 100px;
      }

      img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 0;

        width: 50px;
      }
    }
  }

  .field-range .card-list {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    height: 100%;
    gap: 5px;
  }

  .field-range .card-list :global(.card) {
    height: 90px;
  }

  .field-range.active {
    cursor: pointer;
    -webkit-animation: WoobWoob linear 2s;
    -webkit-animation-iteration-count: infinite;
    -o-animation: WoobWoob linear 2s;
    -o-animation-iteration-count: infinite;
    animation: WoobWoob linear 2s;
    animation-iteration-count: infinite;
  }

  .field-range.close {
    &.field-weather {
      /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+60,709bff+100&amp;0+60,1+100 */
      background: -moz-linear-gradient(top, rgba(255, 255, 255, 0) 60%, #709bff 100%);
      /* FF3.6+ */
      background: -webkit-gradient(linear, left top, left bottom, color-stop(60%, rgba(255, 255, 255, 0)), color-stop(100%, #709bff));
      /* Chrome,Safari4+ */
      background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 60%, #709bff 100%);
      /* Chrome10+,Safari5.1+ */
      background: -o-linear-gradient(top, rgba(255, 255, 255, 0) 60%, #709bff 100%);
      /* Opera 11.10+ */
      background: -ms-linear-gradient(top, rgba(255, 255, 255, 0) 60%, #709bff 100%);
      /* IE10+ */
      background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 60%, #709bff 100%);
      /* W3C */
    }
  }

  .field-range.ranged {
    &.field-weather {
      /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+60,9b9b9b+100&amp;0+60,1+100 */
      background: -moz-linear-gradient(top, rgba(255, 255, 255, 0) 60%, #9b9b9b 100%);
      /* FF3.6+ */
      background: -webkit-gradient(linear, left top, left bottom, color-stop(60%, rgba(255, 255, 255, 0)), color-stop(100%, #9b9b9b));
      /* Chrome,Safari4+ */
      background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 60%, #9b9b9b 100%);
      /* Chrome10+,Safari5.1+ */
      background: -o-linear-gradient(top, rgba(255, 255, 255, 0) 60%, #9b9b9b 100%);
      /* Opera 11.10+ */
      background: -ms-linear-gradient(top, rgba(255, 255, 255, 0) 60%, #9b9b9b 100%);
      /* IE10+ */
      background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 60%, #9b9b9b 100%);
      /* W3C */
    }
  }

  .field-range.siege {
    &.field-weather {
      /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+60,133511+100&amp;0+60,1+100 */
      background: -moz-linear-gradient(top, rgba(255, 255, 255, 0) 60%, #133511 100%);
      /* FF3.6+ */
      background: -webkit-gradient(linear, left top, left bottom, color-stop(60%, rgba(255, 255, 255, 0)), color-stop(100%, #133511));
      /* Chrome,Safari4+ */
      background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 60%, #133511 100%);
      /* Chrome10+,Safari5.1+ */
      background: -o-linear-gradient(top, rgba(255, 255, 255, 0) 60%, #133511 100%);
      /* Opera 11.10+ */
      background: -ms-linear-gradient(top, rgba(255, 255, 255, 0) 60%, #133511 100%);
      /* IE10+ */
      background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 60%, #133511 100%);
      /* W3C */
    }
  }

  // ANIMATIONS
  @-webkit-keyframes WoobWoob {
    0% {
      box-shadow: 0 0 15px #ffbb0b;
    }
    50% {
      box-shadow: 0 0 40px #ffd135;
    }
    100% {
      box-shadow: 0 0 15px #ffbb0b;
    }
  }

  @-o-keyframes WoobWoob {
    0% {
      box-shadow: 0 0 15px #ffbb0b;
    }
    50% {
      box-shadow: 0 0 40px #ffd135;
    }
    100% {
      box-shadow: 0 0 15px #ffbb0b;
    }
  }

  @-ms-keyframes WoobWoob {
    0% {
      box-shadow: 0 0 15px #ffbb0b;
    }
    50% {
      box-shadow: 0 0 40px #ffd135;
    }
    100% {
      box-shadow: 0 0 15px #ffbb0b;
    }
  }

  @keyframes WoobWoob {
    0% {
      box-shadow: 0 0 15px #ffbb0b;
    }
    50% {
      box-shadow: 0 0 40px #ffd135;
    }
    100% {
      box-shadow: 0 0 15px #ffbb0b;
    }
  }
</style>
