<script lang="ts">
    import {onMount} from "svelte";
    import CardProxy from "./BigCard/CardProxy.svelte";
    import type {CardModel, Shiny} from "src/models/card.model";
    import {Config} from "src/store/config.store";
    import Selector from "src/components/Utils/Selector.svelte";

    export let cardKey: string;
    let shinyId: Shiny= "normal";
    let mask = "regular-holo";
    let foil = "cosmos.png";
    let color = "water";

    let card: CardModel;

    const masks = [
      'trainer-gallery-holo',
      'trainer-gallery-v-regular',
      'shiny-rare',
      'v-star',
      'trainer-full-art',
      'secret-rare',
      'v-max',
      'radiant-holo',
      'shiny-v',
      'cosmos-holo',
      'regular-holo',
      'v-regular',
      'trainer-gallery-v-max',
      'shiny-vmax',
      'v-full-art',
      'trainer-gallery-secret-rare',
      'rainbow-holo',
      'reverse-holo',
      'amazing-rare',
      'rainbow-alt',
      'swsh-pikachu'
    ]

    const foils = [
      'ancient.png',
      'angular.png',
      'cosmos.png',
      'cosmos-bottom.png',
      'cosmos-bottom-trans.png',
      'cosmos-middle.gif',
      'cosmos-middle.png',
      'cosmos-middle-trans.png',
      'cosmos-top.png',
      'cosmos-top-trans.png',
      'crossover.png',
      'galaxy.jpg',
      'galaxy-source.png',
      'geometric.png',
      'glitter.png',
      'grain.webp',
      'illusion.png',
      'illusion2.png',
      'illusion-mask.png',
      'metal.png',
      'rainbow.jpg',
      'stylish.png',
      'stylish2.png',
      'trainerbg.jpg',
      'trainerbg.png',
      'vmaxbg.jpg',
      'wave.png'
    ]

    const colors = [
      'water',
      'fire',
      'grass',
      'lightning',
      'psychic',
      'fighting',
      'darkness',
      'metal',
      'fairy',
      'dragon',
    ]

    onMount(() => {
        card = $Config.cards.find((card: CardModel) => card.key === cardKey)!;
    });

</script>

<div class="actions-container">
    <span style="color: black">Mask</span>
    <Selector data={masks} onChange="{(e) => mask = e}"/>
    <span style="color: black">Foil</span>
    <Selector data={foils} onChange="{(e) => foil = e}"/>
    <span style="color: black">Color</span>
    <Selector data={colors} onChange="{(e) => color = e}"/>
</div>
<div class="card-details-container">
    {#if card}
        <CardProxy
                shinyId={shinyId}
                maskId={mask}
                foilId={foil}
                colorId={color}
                id={card.key}
                faction={card.faction}
                img={card.img}
                height="{600}"
        />
    {/if}
</div>

<style>
    .actions-container {
        position: absolute;
        top: 0;
        left: 0;
    }
    .card-details-container {

        display: flex;
        align-items: center;
    }
</style>
