<script lang="ts">
    import {onMount} from "svelte";
    import Modal from "./Modal.svelte";
    import {sendNui} from "src/utils/fetchNui";
    import type {CardModel} from "src/models/card.model";
    import Button from "src/components/Button.svelte";
    import Card from "src/components/Card.svelte";

    export let visible: boolean;

    export let discardPile: CardModel[] = [];

    export let onClose: () => void = () => {
    };

    const onCardClick = (card: CardModel) => {
        sendNui('medic:chooseCardFromDiscard', {
            cardID: card.id
        })
        onClose()
    };

    const onCloseModal = () => {
        sendNui('medic:chooseCardFromDiscard', {})
        onClose();
    };


    onMount(() => {
    });

</script>

<Modal height={400} visible={visible} width={800}>
    <div slot="header">
        Choose one card from your discard pile
    </div>
    <div class="content" slot="content">
        <div class="discard">
            {#each discardPile as card}
                <Card on:click={() => onCardClick(card)} card={card}/>
            {/each}
        </div>
    </div>
    <div class="footer" slot="footer">
        <Button on:click={onCloseModal} theme="dark" title="Close"/>
    </div>
</Modal>

<style lang="scss">
  .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-top: 12px;

    font-size: 24px;

    :global(.cardgame-btn) {
      margin-top: 30px;
    }
  }

  .discard {
    max-height: 100%;

    padding: 10px;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-gap: 10px;
    grid-auto-rows: max-content;
    justify-content: center;


    overflow-y: auto;
    border: 1px solid black;
    border-radius: 5px;

    :global(.card) {
      align-self: center;
      justify-self: center;
      height: 120px;

    }
  }
</style>
