const cards: CardModel[] = fakeCards.map((card) => {
    return new CardModel().fromJson({
        key: card.key,
        name: card.key,
        type: card.type,
        boost: 0,
        power: 1,
        rarity: 'epic',
        category: 1,
        provision_cost: 3,
        faction: card.faction,
        img: card.img,
        ability: [],
    })
});


const catalog: ResponseCatalogData = fakeCards.reduce((prev: ResponseCatalogData, curr: FakeCard) => {
    prev[curr.key] = {
        qty: Math.floor(Math.random() * 3) + 1,
    }

    return prev;
}, {} as ResponseCatalogData)


const leaderboard = new Array(100).fill(0).map((_, i) => ({
    name: `Player ${i}`,
    score: Math.floor(Math.random() * 100),
}));

export {
    cards,
    catalog,
    leaderboard,
}