<script lang="ts">
    import {spring} from "svelte/motion";
    import {createEventDispatcher, onMount} from "svelte";
    import {activeCard} from "src/store/activeCard";
    import {adjust, clamp, round} from "src/utils/Math";
    import type {ImgType} from "src/models/card.model";

    // data / props
    export let id = "";
    export let maskId = "";
    export let foilId = "";
    export let colorId = "";
    export let faction = "";
    export let name = "";
    export let number = "";
    export let set = "";
    export let types: Array<string> | string = "";
    export let imgType: ImgType = 'card';
    export let subtypes = "basic";
    export let supertype = "pokémon";
    export let rarity = "common";

    export let revealOnHover = true;

    let rotation: { x: number, y: number } = revealOnHover ? {x: 180, y: 0} : {x: 0, y: 0};

    export let height = 490; // 40% of 775px
    let width = 529; // width is auto calculated based on height

    // image props
    export let img = "";
    export let back = imgType === 'card' ? "assets/cards/back.jpg" : `assets/booster/${img}` // "https://tcg.pokemon.com/assets/assets/shiny_masks/global/tcg-card-back-2x.jpg";
    export let foil = "";
    export let mask = "";

    // context/environment props
    export let showcase = false;

    const randomSeed = {
        x: Math.random(),
        y: Math.random()
    }

    const cosmosPosition = {
        x: Math.floor(randomSeed.x * 734),
        y: Math.floor(randomSeed.y * 1280)
    };

    let isTrainerGallery = false;

    let back_img = back;
    let front_img = "";
    let img_base = imgType === 'card' ? `assets/cards/${faction}/` : "assets/booster/" //assets/shiny_masks.startsWith("http") ? "" : "https://images.pokemontcg.io/";


    let thisCard: HTMLDivElement;
    let repositionTimer: NodeJS.Timeout;

    let active = false;
    let interacting = false;
    let firstPop = true;
    let loading = true;
    let isVisible = document.visibilityState === "visible";

    const springInteractSettings = {stiffness: 0.066, damping: 0.25}; // stiffness: vitesse de rotation, damping: vitesse de retour
    const springPopoverSettings = {stiffness: 0.033, damping: 0.45};
    let springRotate = spring(rotation, springInteractSettings);
    let springGlare = spring({x: 50, y: 50, o: 0}, springInteractSettings);
    let springBackground = spring({x: 50, y: 50}, springInteractSettings);
    let springRotateDelta = spring({x: 0, y: 0}, springPopoverSettings);
    let springTranslate = spring({x: 0, y: 0}, springPopoverSettings);
    let springScale = spring(1, springPopoverSettings);

    let showcaseInterval: NodeJS.Timer;
    let showcaseTimerStart: NodeJS.Timeout
    let showcaseTimerEnd: NodeJS.Timeout;
    let showcaseRunning = showcase;

    const endShowcase = () => {
        if (showcaseRunning) {
            clearTimeout(showcaseTimerEnd);
            clearTimeout(showcaseTimerStart);
            clearInterval(showcaseInterval);
            showcaseRunning = false;
        }
    };

    const interact = (e: any) => {
        if (revealOnHover) {
            revealOnHover = false;
            rotation = {x: 0, y: 0};
            return (interacting = false);
        }

        // endShowcase();

        if (!isVisible) {
            return (interacting = false);
        }

        // prevent other background cards being interacted with
        if ($activeCard && $activeCard !== thisCard) {
            return (interacting = false);
        }

        interacting = true;

        if (e.type === "touchmove") {
            e.clientX = e.touches[0].clientX;
            e.clientY = e.touches[0].clientY;
        }

        const target = e.target;
        const rect = target.getBoundingClientRect(); // get element's current size/position
        const absolute = {
            x: e.clientX - rect.left, // get mouse position from left
            y: e.clientY - rect.top, // get mouse position from right
        };
        const percent = {
            x: clamp(round((100 / rect.width) * absolute.x)),
            y: clamp(round((100 / rect.height) * absolute.y)),
        };
        const center = {
            x: percent.x - 50,
            y: percent.y - 50,
        };

        updateSprings({
            x: adjust(percent.x, 0, 100, 37, 63),
            y: adjust(percent.y, 0, 100, 33, 67),
            o: 0
        }, {
            x: rotation.x + round(-(center.x / 3.5)),
            y: rotation.y + round(center.y / 2),
            o: 0
        }, {
            x: round(percent.x),
            y: round(percent.y),
            o: 1,
        });
    };

    const interactEnd = (e?: any, delay = 500) => {
        setTimeout(function () {
            const snapStiff = 0.01;
            const snapDamp = 0.06;
            interacting = false;

            springRotate.stiffness = snapStiff;
            springRotate.damping = snapDamp;
            springRotate.set(rotation, {soft: 1});

            springGlare.stiffness = snapStiff;
            springGlare.damping = snapDamp;
            springGlare.set({x: 50, y: 50, o: 0}, {soft: 1});

            springBackground.stiffness = snapStiff;
            springBackground.damping = snapDamp;
            springBackground.set({x: 50, y: 50}, {soft: 1});
        }, delay);
    };

    const deactivate = (e: any) => {
        interactEnd();
        $activeCard = undefined;
    };

    const reposition = (e: any) => {
        clearTimeout(repositionTimer);
        repositionTimer = setTimeout(() => {
            if ($activeCard && $activeCard === thisCard) {
                setCenter();
            }
        }, 300);
    };

    const setCenter = () => {
        const rect = thisCard.getBoundingClientRect(); // get element's size/position
        const view = document.documentElement; // get window/viewport size

        const delta = {
            x: round(view.clientWidth / 2 - rect.x - rect.width / 2),
            y: round(view.clientHeight / 2 - rect.y - rect.height / 2),
        };
        springTranslate.set({
            x: delta.x,
            y: delta.y,
        });
    };

    const popover = () => {
        const rect = thisCard.getBoundingClientRect(); // get element's size/position
        let delay = 100;
        let scaleW = (window.innerWidth / rect.width) * 0.9;
        let scaleH = (window.innerHeight / rect.height) * 0.9;
        let scaleF = 1.75;
        setCenter();
        if (firstPop) {
            delay = 1000;
            springRotateDelta.set({
                x: 360,
                y: 0,
            });
        }
        firstPop = false;
        springScale.set(Math.min(scaleW, scaleH, scaleF));
        interactEnd(null, delay);
    };

    const retreat = () => {
        springScale.set(1, {soft: true});
        springTranslate.set({x: 0, y: 0}, {soft: true});
        springRotateDelta.set({x: 0, y: 0}, {soft: true});
        interactEnd(null, 100);
    };

    const reset = () => {
        interactEnd(null, 0);
        springScale.set(1, {hard: true});
        springTranslate.set({x: 0, y: 0}, {hard: true});
        springRotateDelta.set({x: 0, y: 0}, {hard: true});
        springRotate.set(rotation, {hard: true});
    };

    $: {
        if ($activeCard && $activeCard === thisCard) {
            popover();
            active = true;
        } else {
            retreat();
            active = false;
        }
    }


    $: foilStyles = `
    --foil: url(${window.location.href}assets/shiny_masks/${foilId}');
    `;
    const staticStyles = `
    --seedx: ${randomSeed.x};
    --seedy: ${randomSeed.y};
    --cosmosbg: ${cosmosPosition.x}px ${cosmosPosition.y}px;
    height: 100%;
  `;
    $: dynamicStyles = `
    --pointer-x: ${$springGlare.x}%;
    --pointer-y: ${$springGlare.y}%;
    --pointer-from-center: ${
        clamp(Math.sqrt(
            ($springGlare.y - 50) * ($springGlare.y - 50) +
            ($springGlare.x - 50) * ($springGlare.x - 50)
        ) / 50, 0, 1)};
    --pointer-from-top: ${$springGlare.y / 100};
    --pointer-from-left: ${$springGlare.x / 100};
    --card-opacity: ${$springGlare.o};
    --rotate-x: ${$springRotate.x + $springRotateDelta.x}deg;
    --rotate-y: ${$springRotate.y + $springRotateDelta.y}deg;
    --background-x: ${$springBackground.x}%;
    --background-y: ${$springBackground.y}%;
    --card-scale: ${$springScale};
    --translate-x: ${$springTranslate.x}px;
    --translate-y: ${$springTranslate.y}px;
	`;

    $: cardSizeStyles = `
    height: ${height}px;
    width: ${width}px;
    `;

    $: cardBackStyles = `
    height: ${height}px;
    width: ${width}px;
    ${imgType === 'booster' ? 'opacity: 0;' : ''}
    `;

    $: {
      width = height * 410 / 775;
        rarity = rarity.toLowerCase();
        supertype = supertype.toLowerCase();
        number = number.toLowerCase();
        isTrainerGallery = !!number.match(/^[tg]g/i) || !!(id === "swshp-SWSH076" || id === "swshp-SWSH077");
        if (Array.isArray(types)) {
            types = types.join(" ").toLowerCase();
        }
        if (Array.isArray(subtypes)) {
            subtypes = subtypes.join(" ").toLowerCase();
        }
    }

    type PosSpring = { x: number, y: number, o: number };

    const updateSprings = (background: PosSpring, rotate: PosSpring, glare: PosSpring) => {

        springBackground.stiffness = springInteractSettings.stiffness;
        springBackground.damping = springInteractSettings.damping;
        springRotate.stiffness = springInteractSettings.stiffness;
        springRotate.damping = springInteractSettings.damping;
        springGlare.stiffness = springInteractSettings.stiffness;
        springGlare.damping = springInteractSettings.damping;

        springBackground.set(background);
        springRotate.set(rotate);
        springGlare.set(glare);

    }

    $: {
        if ($activeCard && $activeCard === thisCard) {
            interacting = true;
            //orientate($orientation);
        }
    }

    document.addEventListener("visibilitychange", (e) => {
        isVisible = document.visibilityState === "visible";
        endShowcase();
        reset();
    });

    const imageLoader = (e: any) => {
        loading = false;
        if (mask || foil) {
            foilStyles = `
    --mask: url(${mask});
    --foil: url(${foil});
      `;
        }

    };

    onMount(() => {
        // set the front image on mount so that
        // the lazyloading can work correctly
        front_img = img_base + img + (imgType === 'card' ? ".png" : "");
    });

    const eventDispatcher = createEventDispatcher();

    function onClick() {
        eventDispatcher("click", id);
    }
</script>

<svelte:window on:scroll={reposition}/>

<div
        bind:this={thisCard}
        class="big_card {colorId} {maskId} / interactive / "
        class:active
        class:interacting
        class:loading
        class:masked={!!mask}
        on:click={onClick}
        on:keypress={onClick}
        style={dynamicStyles}
>
    <div
            class="big_card__translater">
        <button
                style="box-shadow: {imgType === 'card' ? '' : 'none'};"
                aria-label="Expand the Card; {name}."
                class="big_card__rotator"
                on:blur={deactivate}
                on:mouseout={interactEnd}
                on:pointermove={interact}
                tabindex="0"
        >
            <img
                    style="{cardBackStyles}"
                    alt="The back of the Card"
                    class="big_card__back"
                    loading="lazy"
                    src={back_img}
            />
            <div class="big_card__front"
                 style={ staticStyles}>
                <img
                        style="{cardSizeStyles}"
                        alt="Front design of the {name} Card"
                        loading="lazy"
                        on:load={imageLoader}
                        src={front_img}
                />
                {#if imgType === "card"}
                    <div style="{cardSizeStyles  + foilStyles }"
                         class="big_card__shine"></div>
                    <div style="{cardSizeStyles}"
                         class="big_card__glare"></div>
                {/if}
            </div>
        </button>
    </div>
</div>

<style>

    .big_card {
        height: var(--card-height, unset);
    }

    :root {
        --pointer-x: 50%;
        --pointer-y: 50%;
        --card-scale: 1;
        --card-opacity: 0;
        --translate-x: 0px;
        --translate-y: 0px;
        --rotate-x: 180deg;
        --rotate-y: 0deg;
        --background-x: var(--pointer-x);
        --background-y: var(--pointer-y);
        --pointer-from-center: 0;
        --pointer-from-top: var(--pointer-from-center);
        --pointer-from-left: var(--pointer-from-center);
    }

</style>
