import {catalogSearchMode, currentRoute, currentRouteParams, deckEditMode} from "src/store/config.store";
import {
    chooseSide,
    emreis_leader4,
    hasPlayedCard,
    inGame,
    isReDrawing,
    medicDiscard,
    passing,
    room,
    roomFoeSide,
    roomSide,
    setAgile,
    setHorn,
    spectator,
    waiting
} from "src/store/game.store";
import {cardPreview} from "src/store/cardPreview.store";
import {notification} from "src/store/notification.store";

export default function reset() {
    console.log("Reset UI stores")

    cardPreview.set(null);

    notification.set(null);

    deckEditMode.set(false);
    catalogSearchMode.set(false);
    currentRouteParams.set(null);
    currentRoute.set(null);
    chooseSide.set(false);
    roomSide.set(undefined);
    roomFoeSide.set(undefined);
    room.set(undefined);
    spectator.set(false);
    inGame.set(false);
    waiting.set(false);
    passing.set(false);
    medicDiscard.set(undefined);
    emreis_leader4.set(undefined);
    setAgile.set(undefined);
    setHorn.set(undefined);
    isReDrawing.set(false);
    hasPlayedCard.set(false);
}