<script lang="ts">
    import {onDestroy, onMount} from "svelte";
    import Profile from "src/components/Battle/Profile.svelte";
    import Field from "src/components/Battle/Field.svelte";
    import Button from "src/components/Button.svelte";
    import DiscardModal from "src/components/Modals/DiscardModal.svelte";
    import {
        chooseSide,
        emreis_leader4,
        isReDrawing,
        medicDiscard,
        passing,
        room,
        roomSide,
        setAgile,
        setHorn,
        spectator,
        waiting
    } from "src/store/game.store";
    import {sendNui} from "src/utils/fetchNui";
    import MedicModal from "src/components/Modals/MedicModal.svelte";
    import ChooseSideModal from "src/components/Modals/ChooseSideModal.svelte";
    import EmreisLeader4Modal from "src/components/Modals/EmreisLeader4Modal.svelte";
    import RedrawModal from "src/components/Modals/RedrawModal.svelte";
    import {useNuiEvent} from "src/utils/useNuiEvent";
    import NotificationHandler from "src/utils/notification";
    import {CardModel} from "src/models/card.model";
    import {SideInfo} from "src/models/side.model";
    import BetterHand from "src/components/Battle/BetterHand.svelte";
    import WinnerModal from "src/components/Modals/WinnerModal.svelte";
    import Card from "src/components/Card.svelte";

    let handCards: Array<CardModel> = [];

    let yourSide: SideInfo = new SideInfo();
    let otherSide: SideInfo = new SideInfo()

    let redrawVisible = false;
    let discardVisible = false;
    let chooseSideVisible = false;
    let medicVisible = false;
    let emreisLeader4Visible = false;

    let winner: string | null = null;

    const redrawingUnsubscribe = isReDrawing.subscribe(value => {
        redrawVisible = value;
    });

    const chooseSideUnsubscribe = chooseSide.subscribe(value => {
        chooseSideVisible = value;
    });

    const medicUnsubscribe = medicDiscard.subscribe(value => {
        medicVisible = !!value;
    });

    const emreisUnsubscribe = emreis_leader4.subscribe(value => {
        emreisLeader4Visible = !!value;
    });

    useNuiEvent('set:waiting', (data) => {
        console.log("set waiting", data.waiting)
        waiting.set(data.waiting);
    });

    useNuiEvent('set:passing', (data) => {
        console.log("set passing", data.passing)
        passing.set(data.passing);
    });

    useNuiEvent('foe:left', () => {
        NotificationHandler.error('Your opponent has left the game', 'Opponent left');
    });

    useNuiEvent('played:medic', (data) => {
        const cards: CardModel[] = JSON.parse(data.cards).map((card: object) => new CardModel().fromJson(card));

        console.log("played medic", JSON.stringify(data.cards))

        medicDiscard.set({
            cards: cards,
        });
    });

    useNuiEvent('played:emreis_leader4', (data) => {
        const cards: CardModel[] = JSON.parse(data.cards).map((card: object) => new CardModel().fromJson(card));

        console.log("played emreis leader 4",  JSON.stringify(data.cards))

        emreis_leader4.set({
            cards: cards,
        });
    });

    useNuiEvent('played:agile', (data) => {
        console.log("played agile", data.cardID)
        setAgile.set(data.cardID);
    });

    useNuiEvent('played:horn', (data) => {
        console.log("played horn", data.cardID)
        setHorn.set(data.cardID);
    });

    useNuiEvent('redraw:cards', () => {
        console.log("redraw cards")
        isReDrawing.set(true);
    });

    useNuiEvent('redraw:close', () => {
        console.log("redraw close")
        isReDrawing.set(false);
    });

    useNuiEvent('update:hand', (data) => {
        console.log("update hand")
        //console.log("update hand", data.cards);
        if ($roomSide === data.roomSide) {
            handCards = JSON.parse(data.cards).map((card: object) => new CardModel().fromJson(card));
        }
    });

    useNuiEvent('update:info', (data) => {
        console.log("update profile info")
        //console.log("update info", JSON.stringify(data.info));
        if ($roomSide === data.roomSide) {
            yourSide = new SideInfo().fromJson({
                ...yourSide,
                info: {
                    ...yourSide.info,
                    ...data.info,
                },
                leader: data.leader,
            });
        } else {
            otherSide = new SideInfo().fromJson({
                ...otherSide,
                info: {
                    ...otherSide.info,
                    ...data.info,
                },
                leader: new CardModel().fromJson(data.leader),
            });
        }
    });

    useNuiEvent('update:fields', (data) => {
        console.log("update fields")
        if ($roomSide === data.roomSide) {
            yourSide = new SideInfo().fromJson({
                ...yourSide,
                field: {
                    ...yourSide.field,
                    close: data.close,
                    ranged: data.ranged,
                    siege: data.siege,
                    weather: data.weather,
                }
            });
        } else {
            otherSide = new SideInfo().fromJson({
                ...otherSide,
                field: {
                    ...otherSide.field,
                    close: data.close,
                    ranged: data.ranged,
                    siege: data.siege,
                    weather: data.weather,
                }
            });
        }
    });

    useNuiEvent('gameover', (data) => {
        console.log("gameover", data.winner)
        winner = data.winner;
    });

    const quitGame = () => {
        console.log("quit game from gameover");
        sendNui('request:leaveRoom');
        sendNui('close');
    }


    onMount(() => {
        if (!$spectator) {
            sendNui('request:gameLoaded', {
                roomID: $room
            })
        }
    });

    onDestroy(() => {
        redrawingUnsubscribe();
        chooseSideUnsubscribe();
        medicUnsubscribe();
        emreisUnsubscribe();
    });
</script>

<div class="battle-container">
    <div class="profile-container">
        <Profile info={otherSide.info} isPlayer={false} leader={otherSide.leader}/>
        <div class="weather-card-list">
            {#each yourSide.field.weather.cards as card}
                <Card card={card}/>
            {/each}
        </div>
        <Profile info={yourSide.info} isPlayer={true} leader={yourSide.leader}/>
    </div>

    <div class="board-container">
        <Field data={otherSide.field} isPlayer={false}/>
        <Field data={yourSide.field} isPlayer={true}/>
    </div>

    <div class="action-container">
        {#if $waiting}
            {#if $spectator}
                <div class="wait-as-spectator">
                    {otherSide.info.name}'s Turn
                </div>
            {:else}
                <div class="wait-for-foe">
                    Opponent's Turn
                </div>
            {/if}
        {:else}
            {#if $spectator}
                <div class="wait-as-spectator">
                    {yourSide.info.name}'s Turn
                </div>
            {:else}
                <div class="wait-for-your">
                    Your Turn
                </div>
            {/if}
        {/if}

        {#if !$spectator}
            <Button on:click={() => discardVisible = true} title="See Discard"/>
        {/if}
    </div>
</div>

{#if !$spectator}
    <BetterHand cardList={handCards}/>
{/if}

<RedrawModal cardList={handCards} onClose={() => redrawVisible = false} visible={redrawVisible}/>
<DiscardModal discardPile={yourSide.info.discard} onClose={() => discardVisible = false} visible={discardVisible}/>
<MedicModal discardPile={$medicDiscard?.cards ?? []} onClose={() => medicVisible = false} visible={medicVisible}/>
<ChooseSideModal onClose={() => chooseSideVisible = false} visible={chooseSideVisible}/>
<EmreisLeader4Modal discardPile={$emreis_leader4?.cards ?? []} onClose={() => emreisLeader4Visible = false}
                    visible={emreisLeader4Visible}/>
<WinnerModal onClose={quitGame} visible={winner !== null} winner={winner ?? "nobody"}/>

<style lang="scss">
  .battle-container {
    background-image: url("/assets/ui/wood3.png");
    background-size: 1300px 800px;
    background-repeat: no-repeat;
    background-position: center;

    box-sizing: border-box;
    display: flex;
    justify-content: center;
    width: 1300px;
    height: 800px;

    border-radius: 8px;
    padding: 32px 48px;
  }

  .profile-container {
    flex-grow: 1;
    max-width: 20%;
    border-radius: 8px;
    //border: 1px solid #000;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    .weather-card-list {
      margin-left: auto;
      margin-right: 5px;
      display: flex;
      align-items: center;
      gap: 5px;

      :global(.card) {
        height: 100px;
      }
    }
  }

  .board-container {
    flex-grow: 2;
    border-radius: 8px;
    //border: 1px solid #000;

    display: flex;
    flex-direction: column;
    gap: 5px;
  }

  .action-container {
    //flex-grow: 1;
    width: 200px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .wait-for-foe, .wait-for-you {
      pointer-events: none;
      user-select: none;
      margin-top: 20px;
      width: 80%;
      color: #fff;
      text-align: center;
      text-shadow: 0 0 5px #000;
      padding: 5px;
      border-radius: 5px;
    }

    .wait-for-foe {
      background-color: #fa0707;
    }

    .wait-for-you {
      background-color: #8db600;
    }

    .wait-as-spectator {
      background-color: #00a8ff;
    }

    :global(.cardgame-btn) {
      height: 40px;
      width: 80%;

      margin-top: auto;
      margin-bottom: 15px;
    }
  }

</style>
