<script lang="ts">
  import { onMount } from 'svelte';
  import cards from "../cards.json";

  export let data: Array<any> = [];
  export let onChange: (shinyId: string) => void;

  let index = 0;

  const offset = 1;

  let validList: Array<number> = [];

  const next = () => {
    index = (index + offset) % data.length;

    onChange(data[index]);
  }

    const prev = () => {
        index = (index - offset + data.length) % data.length;

        onChange(data[index]);
    }

    const handleKey = (event: KeyboardEvent) =>  {
      switch (event.key) {
        case "ArrowDown":
          //validList.push(index);
          //validList = validList
          console.log("push item")
          break;
        case "ArrowUp":
          //validList = validList.filter((item) => item !== index);
          break;
        case "ArrowLeft":
          prev();
          break;
        case "ArrowRight":
          next();
          break;
        case "Enter":
          //console.log(validList.map((id) => cards[id]));
          break;
        case "Escape":
          // Faire quelque chose pour la touche "esc" pressée.
          break;
        default:
          return; // Quitter lorsque cela ne gère pas l'événement touche.
      }
  }

  onMount(() => {

  });

</script>

<div class="style-actions">
    <button class="btn btn-primary" on:click={prev} on:keydown={handleKey}>⬅️</button>
    <span>{index + 1} / {data.length}</span>
    <button class="btn btn-primary" on:click={next} on:keydown={handleKey}>➡️</button>
    <h3>{data[index]}</h3>
</div>
<span class="registered">
    {validList.map((id) => id.toString()).join(", ")}
</span>


