<script lang="ts">
    import {onDestroy, onMount} from 'svelte';
    import type {NotificationEvent} from "../store/notification.store";
    import {notification, notificationStyles} from "../store/notification.store";
    import {useNuiEvent} from "src/utils/useNuiEvent";
    import NotificationHandler from "src/utils/notification";
    import {debugData} from "src/utils/debugData";

    type Notification = {
        id: number;
        message: NotificationEvent;
        timeout: NodeJS.Timeout;
    }

    let notifications: Notification[] = [];

    const unSubscriber = notification.subscribe((newNotification) => {
        if (!newNotification) return;

        const id = notifications.push({
            id: notifications.length + 1,
            message: newNotification,
            timeout: setTimeout(() => {
                notifications = notifications.filter((n) => n.id !== id);
            }, newNotification.duration)
        });

        notifications = [...notifications];
    });

    useNuiEvent('notification', data => {
        console.log('notification', data.message);
        NotificationHandler.info(data.message);
    });

    const removeNotification = (id: number) => {
        notifications = notifications.filter((n) => {
            if (n.id === id) {
                clearTimeout(n.timeout);
                return false;
            }
            return true;
        });
    }

    onDestroy(() => {
        unSubscriber();
    });


    onMount(() => {
    });
</script>

<main>
    <div class="notification-container">
        {#each notifications as {message, id}}
            <div class="notification" style="background-color: {notificationStyles[message.type]}"
                 on:keypress={() => removeNotification(id)}
                 on:click={() => removeNotification(id)}>
                <div class="notification__content">
                    <div class="notification__title">{message.title}</div>
                    <div class="notification__message">{message.message}</div>
                </div>
            </div>
        {/each}
    </div>
    <slot/>
</main>

<style lang="scss">
    .notification-container {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        z-index: 99999999;

        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .notification {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 800px;
        padding: 12px 0;
        background-color: #fff;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
        pointer-events: all;
        color: black;

      &:last-child {
        border-radius: 0 0 10px 10px;
      }
    }

    .notification__content {
        display: flex;
        gap: 12px;
        /*flex-direction: column;*/
        justify-content: center;
        align-items: center;
    }

    .notification__title {
        font-size: 20px;
        font-weight: 600;
        /*margin-bottom: 10px;*/
    }

    .notification__message {
        font-size: 16px;
        font-weight: 400;
    }
</style>
