<script lang="ts">
    import {onMount} from "svelte";
    import FieldRange from "src/components/Battle/FieldRange.svelte";
    import type {RangeDistance} from "src/models/field.model";
    import {SideField} from "src/models/side.model";

    export let data: SideField = new SideField();
    export let isPlayer = false;

    let order: RangeDistance[] = ["close", "ranged", "siege"];

    onMount(() => {
        if (!isPlayer) {
            order = order.reverse();
        }
    });

</script>

<div class="field">
    {#each order as range}
        <FieldRange range={range} field={data[range]} weatherCards={data.weather.cards} isPlayer={isPlayer}/>
    {/each}
</div>

<style>
    .field {
        display: flex;
        flex-direction: column;
        gap: 5px;

        width: 100%;
        height: 100%;
    }


</style>
