    // data / pokemon props
    export let shinyId: keyof typeof cards = "normal";
    export let maskId: string | undefined = undefined;
    export let foilId: string | undefined = undefined;
    export let colorId: string | undefined = undefined;

    let style: CardStyle = (cards as unknown as ShinyList)[shinyId || "normal"];
     export let id: string;
    export let faction: string | undefined = undefined
   /* export let name: string | undefined = undefined;
    export let number: string | undefined = undefined;
    export let set: string | undefined = undefined;
    export const types: Array<string> | string | undefined = undefined;*/
    export let imgType: ImgType = 'card';
   /* export let subtypes: string[] | undefined = undefined;
    export let supertype: string | undefined = undefined;
    export let rarity: string | undefined = undefined;*/
    export let isReverse = false;

    export let revealOnHover = false;

    export let height: number = 490;

    // image props
    export let img: string | undefined = undefined;
    export let back: string | undefined = undefined;
    /*export let foil: string | undefined = undefined;
    export let mask: string | undefined = undefined;
*/
    // context/environment props
    export let showcase = false;

    //const server = import.meta.env.VITE_CDN;
    /**
     * Shiny Vault BigCard (starts with sv)
     */
    const isShiny = !!style.number && style.number.toLowerCase().startsWith("sv");
    /**
     Trainer / Galar Gallery BigCard (not shiny)
     */
    const isGallery = !!style.number && !!style.number.match(/^[tg]g/i);
    /**
     Alternate Art BigCard (not shiny / gallery)
     */
    const isAlternate = !!id && altArts.includes(style.id) && !isShiny && !isGallery;
    /**
     Promo BigCard
     */
    const isPromo = !!style.set && style.set === "swshp";

    if (isReverse) {
      style.rarity = style.rarity + " Reverse Holo";
    }

    if (isGallery) {
        if (!!style.rarity && style.rarity?.startsWith("Trainer Gallery")) {
          style.rarity = style.rarity?.replace(/Trainer Gallery\s*/, "");
        }
        if (!!style.rarity && style.rarity?.includes("Rare Holo V") && !!style.subtypes && style.subtypes?.includes("VMAX")) {
          style.rarity = "Rare Holo VMAX";
        }
        if (!!style.rarity && style.rarity?.includes("Rare Holo V") && !!style.subtypes && style.subtypes?.includes("VSTAR")) {
          style.rarity = "Rare Holo VSTAR";
        }
    }

    if (isPromo) {
        if (style.id === "swshp-SWSH076" || style.id === "swshp-SWSH077") {
          style.rarity = "Rare Secret";

        } else if (!!style.subtypes && style.subtypes?.includes("V")) {
          style.rarity = "Rare Holo V";
        } else if (!!style.subtypes && style.subtypes?.includes("V-UNION")) {
          style.rarity = "Rare Holo VUNION";
        } else if (!!style.subtypes && style.subtypes?.includes("VMAX")) {
          style.rarity = "Rare Holo VMAX";
        } else if (!!style.subtypes && style.subtypes?.includes("VSTAR")) {
          style.rarity = "Rare Holo VSTAR";
        } else if (!!style.subtypes && style.subtypes?.includes("Radiant")) {
          style.rarity = "Radiant Rare";
        }
    }

    function cardImage() {
        if (!!img) {
            return img;
        }

        return "";
    }

