            etch = "etched";
            style = "sunpillar";
        }

        if (fRarity === "amazing rare" || fRarity === "rare rainbow" || fRarity === "rare secret") {
            etch = "etched";
            style = "swsecret";
        }

        if (isShiny) {

            etch = "etched";
