                style = "swsecret";
                rarity = "Rare Rainbow Alt";

            } else {

                style = "sunpillar";

            }

        }

        if (isPromo) {

            let promoStyle = promos[id];
            if (promoStyle) {
                style = promoStyle.style.toLowerCase();
                etch = promoStyle.etch.toLowerCase();
