        return `${server}/foils/${fSet}/${type}/upscaled/${fNumber}_foil_${etch}_${style}_2x.${ext}`;

    }*/

   /* function foilImage() {
      //return "/assets/shiny_masks/cosmos.png";
      return foilMaskImage(foil, "foils");
    }*/

    /*function maskImage() {
        return foilMaskImage(mask, "masks");
    }*/

    function refreshProxy() {
      proxy = {

        img: cardImage(),
        back,
        // foil: foilImage(),
        // mask: maskImage(),

        // id,
        faction,
        // name,
        // number,
        // set,
        // types,
        imgType,
        // subtypes,
        // supertype,
        // rarity,
        showcase,
        revealOnHover,
        height,

        ...style,

        maskId,
        foilId,
        colorId,

      }
    }

    $: {
      if (shinyId || maskId || foilId || colorId) {
        console.log("refreshing proxy")
        style = JSON.parse(JSON.stringify(cards[shinyId]));
        refreshProxy();
      }
    }

    let proxy = {}

    onMount(() => {

      refreshProxy();
    })

    const eventDispatcher = createEventDispatcher();

    function onClick() {
        eventDispatcher("click", id);
    }

</script>


<Card {...proxy} on:click={onClick}/>