<script lang="ts">
    import {onMount} from "svelte";
    import {useNuiEvent} from "src/utils/useNuiEvent";
    import type {Deck} from "src/models/deck.model";
    import {Config, deckEditMode} from "src/store/config.store";
    import type {CardModel} from "src/models/card.model";
    import Card from "./Card.svelte";
    import NotificationHandler from "src/utils/notification";
    import {sendNui} from "src/utils/fetchNui";
    import type {Faction} from "src/models/faction.model";

    let currentDeck = 0;
    let editedDeck: number | null = null;
    let currentFaction: Faction = 'ambarino';


    let deckCount = {
        count: 0,
        max: 150
    };


    let decks: Array<Deck> = [];
    let catalog: Array<CardModel> = $Config.cards;
    let availableCards: Array<CardModel> = [];

    useNuiEvent('response:decks', (data) => {
        const responseDecks = data.decks;
        decks = Object.entries(responseDecks).map(deck => ({
            ...deck[1],
            cards: deck[1].cards.map(c => catalog.find(cc => cc.key === c)) as CardModel[],
            id: deck[0].toString(),
            name: deck[1].name,
            faction: deck[1].faction,

            invalid: !!deck[1].invalidCards && deck[1].invalidCards.length > 0,
        }));

        calculateDeckCount();
        filterUnusedCards();
    });

    useNuiEvent('response:catalog', (data) => {
        const responseCatalog = data.catalog;
        const catalogKeys = Object.keys(responseCatalog);

        catalog = catalog.filter(card => !!card.key && catalogKeys.includes(card.key));

        // filter out cards that are not in the catalog
        decks = decks.map(deck => ({
            ...deck,
            cards: deck.cards.filter(c => !!c.key && catalogKeys.includes(c.key))
        }));


        calculateDeckCount();
        filterUnusedCards();
    });

    const onCardClick = (card: CardModel, section: "catalog" | "deck") => {
        const deck = decks[currentDeck];

        if (section === 'catalog') {
            const newCard = catalog.find(c => c.key === card.key);
            if (!!newCard) {
                addCardToDeck(newCard);
            }
        } else if (section === 'deck') {
            const foundCard = deck.cards.findIndex(c => c.key === card.key);
            if (foundCard > -1) {
                deck.cards.splice(foundCard, 1);
            }
        }

        decks = decks;
        calculateDeckCount();
        filterUnusedCards();
    }

    const addCardToDeck = (card: CardModel) => {
        if (decks[currentDeck] === undefined) {
            return
        }
        const deck = decks[currentDeck];
        const leaderIdx = deck.cards.findIndex(c => c.type === 3);

        // BigCard is Leader. Need to check if there is not already a leader in the deck
        if (card.type === 3) {

            // if there is alrady a leader in the deck, remove it
            if (leaderIdx > -1) {
                deck.cards.splice(leaderIdx, 1);
            }

            // remove all cards that are not from the right faction, except ambarino.
            deck.cards = deck.cards.filter(c => [card.faction, "ambarino"].includes(c.faction));

            deck.cards.push(card);
            deck.faction = card.faction!;
            return
        }

        if (leaderIdx > -1) {
            const leader = deck.cards[leaderIdx];

            if (card.faction !== 'ambarino' && card.faction !== leader.faction) {
                //new NotificationView({message: "BigCard is not the same Faction as Leader card", error: true}).render();
                return;
            }
        }

        if (deckCount.count + card.provision_cost! > deckCount.max) return;

        deck.cards.push(card);

        decks = decks;
    }

    const getFreeDeckId = () => {
        return `tempdeckid${decks.length + 1}`;
    }

    const filterUnusedCards = () => {
        if (decks[currentDeck] === undefined) {
            availableCards = [];
            return;
        }

        const deck = decks[currentDeck];

        let available = catalog.filter(c => deck.cards.filter(dc => dc.key === c.key).length < (c.category === 0 ? 3 : 1))

        availableCards = sortCardList(available.filter(c => c.faction === currentFaction));
    }

    const sortCardList = (cardList: CardModel[]) => {
        return cardList.sort((a, b) => {
            if (a.type !== b.type) {
                if (a.type === 3) {
                    return -1;
                } else if (b.type === 3) {
                    return 1;
                }
                return a.type! - b.type!;
            } else {
                return a.key!.localeCompare(b.key!);
            }
        });
    }

    const onCreate = () => {
        const newDeckId = getFreeDeckId();

        currentDeck = decks.push({
            id: newDeckId,
            name: `New Deck ${decks.length + 1}`,
            faction: 'ambarino',
            invalid: false,
            invalidCards: [],
            cards: []
        }) - 1;

        decks = decks; // required to trigger component reactivity. see https://svelte.dev/tutorial/updating-arrays-and-objects
        calculateDeckCount();
        filterUnusedCards();
    }

    const onDelete = () => {
        if (decks[currentDeck] === undefined) {
            //new NotificationView({message: "Unknown selected Deck.", error: true}).render();
            return
        }

        decks = decks.filter((d, idx) => idx !== currentDeck);
        currentDeck = currentDeck - 1 < 0 ? 0 : currentDeck - 1;

        calculateDeckCount();
        filterUnusedCards();
    }

    const onDeckChange = (deckId: string) => {
        //const element = $(e.target).closest(".deck-list-item");
        //const deckName = element.data("id").toString();


        const deckIdx = decks.findIndex(d => d.id === deckId);

        if (editedDeck === deckIdx) {
            return
        }

        if (deckIdx >= 0) {
            editedDeck = null;
            currentDeck = deckIdx;
        }

        calculateDeckCount();
        filterUnusedCards();
    }

    const onDeckTitleEdit = (deckId: string) => {
        const deckIdx = decks.findIndex(d => d.id === deckId);

        if (deckIdx >= 0) {
            if (editedDeck === deckIdx) {
                //$(e.target).closest(".edit-title").focusout();
            }
            editedDeck = editedDeck === deckIdx ? null : deckIdx;
            deckEditMode.set(editedDeck !== null);
        }
    }


    const onDeckTitleChange = (deckId: string, event: Event) => {
      event.preventDefault();
      event.stopPropagation();
      event.stopImmediatePropagation();
        const value = event.target as HTMLFormElement;
        if (editedDeck == null || decks[editedDeck] === undefined) {
            return
        }

        const deck = decks[editedDeck];
        editedDeck = null;

        if (deck.id !== deckId) {
            return;
        }

        const formData = new FormData(value);

        deck.name = formData.get('title') as string;
        deckEditMode.set(false);
    }

    const onFactionChange = (factionId: Faction) => {
        const faction = $Config.factions.find(f => f.id === factionId);

        if (!!faction) {
            currentFaction = factionId;
            filterUnusedCards();
        }
    }

    const onSave = () => {
        if (decks[currentDeck] === undefined) {
            NotificationHandler.error("Unknown selected Deck.");
            return
        }

        const deck = decks[currentDeck];

        if (!!deck.invalidCards && deck.invalidCards.length > 0) {
            for (const card of deck.cards) {
                if (deck.invalidCards.includes(card.key!)) {
                    NotificationHandler.error("The deck contains cards that are not in your catalog.");
                    return
                }
            }
        }


        if (deck.cards.length < 25) {
            NotificationHandler.error("Not enough cards in deck.\nA deck should contain at least 25 cards.");
            return
        }

        const leaderIdx = deck.cards.findIndex(c => c.type === 3);
        if (leaderIdx === -1) {
            NotificationHandler.error("Please add a leader to your Deck.");
            return
        }

        const formattedDeck = {
            ...deck,
            cards: deck.cards.map(c => c.key)
        }

        sendNui('save:deck', formattedDeck)
        NotificationHandler.success("Saved !");
    }

    const calculateDeckCount = () => {
        if (decks[currentDeck] === undefined) {
            deckCount = {
                count: 0,
                max: 150
            };
            return
        }

        const deck = decks[currentDeck];

        const leaderCard = deck.cards.find(c => c.type === 3);
        const leaderCost = !!leaderCard ? leaderCard.provision_cost! : 0;
        const cardCount = deck.cards.reduce((a, b) => {
            return a + b.provision_cost!;
        }, 0) - leaderCost;

        deckCount = {
            count: cardCount,
            max: 150 + leaderCost
        };
    }

    onMount(() => {
    });

</script>

<div class="deck-building-container">
    <div class="container-landing">
        <div class="deck-list-container">
            <ul class="deck-list">
                {#each decks as deck, idx}
                    <li class="deck-list-item" class:selected={idx === currentDeck}
                        on:keypress={() => onDeckChange(deck.id)}
                        on:click={() => onDeckChange(deck.id)}
                    >
                        {#if idx === editedDeck}
                            <form class="deck-edit-form"
                                  on:submit|preventDefault={(e) => onDeckTitleChange(deck.id, e)}>
                                <input class="deck-item-title edit-title" name="title" autofocus>
                                <button type="submit" class="deck-item-edit-btn">✓
                                </button>
                            </form>
                        {:else}
                            <span class="deck-item-title">{deck.name}
                                {#if deck.invalid}(invalid){/if}</span>
                            <button class="deck-item-edit-btn" on:click={() => onDeckTitleEdit(deck.id)}>...</button>
                        {/if}
                    </li>
                {/each}
            </ul>
        </div>
        <div class="creator-container">
            <div class="creator-action-container">
                <button class="action-btn creator-create-btn" on:click={onCreate}>New Deck</button>
                <button class="action-btn creator-delete-btn" on:click={onDelete}>Delete Deck</button>
                <div class="deck-info">
                    <div class="deck-card-count">Nb Cards: {(decks[currentDeck]?.cards ?? []).length}</div>
                    <div class="deck-score-count">Score: {deckCount.count} / {deckCount.max}</div>
                </div>
                <button class="action-btn creator-cancel-btn">Cancel</button>
                <button class="action-btn creator-save-btn" on:click={onSave}>Save</button>
            </div>
            <div class="card-list card-list-deck" data-id="deck">
                {#each sortCardList(decks[currentDeck]?.cards ?? []) as card}
                    <Card card={card} on:click={() => onCardClick(card, 'deck')}/>
                {/each}
            </div>
            <div class="class-list-filter">
                {#each $Config.factions as faction}
                    <button class="filter-btn" class:selected={faction.id === currentFaction}
                            on:click={() => onFactionChange(faction.id)}>{faction.label}</button>
                {/each}
            </div>
            <div class="card-list card-list-available" data-id="catalog">
                {#each availableCards as card}
                    <Card card={card}
                          diff={false}
                          invalid={decks[currentDeck].invalidCards.includes(card.key ?? "")}
                          on:click={() => onCardClick(card, 'catalog')}
                    />
                {/each}
            </div>
        </div>
    </div>
    <div class="deck-rules">
        <h3>Deck Building Rules</h3>
        <p>- A deck if composed of at least 25 cards.</p>
        <p>- The total cost of a deck cannot exceed 150 + the bonus given by the leader.</p>
        <p>- Only one Leader is allowed per deck.</p>
        <p>- Bronze cards can be added 3 times to a deck.</p>
        <p>- Gold cards can be only be added 1 time to a deck.</p>
        <p>- A deck can ony be composed of cards the same faction of the leader + ambarino cards.</p>
    </div>
</div>

<style lang="scss">
    .deck-building-container {
        height: 800px;
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .container-landing {
        background-image: url("/assets/ui/wood3.png");
        background-size: 1400px 800px;
        background-repeat: no-repeat;
        background-position: center;
        display: flex;

        max-width: 1300px;
        min-width: 1300px;

        padding: 32px 64px;

    }

    .deck-list-container {
        padding: 10px;
        width: 200px;

        display: flex;
        justify-content: center;

        border-right: 1px solid white;
    }

    .creator-container {
        width: 100%;

        display: flex;
        flex-direction: column;
        justify-content: flex-start;
    }

    .card-list {
        padding: 8px;
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
        justify-content: flex-start;
        overflow-y: auto;
    }

    .card-list-deck, .card-list-available {
        height: 45%;
        padding: 10px;

    }

    .card-list-deck {
        border-bottom: 1px solid white;
    }

    .deck-list {
        width: 100%;
        padding: 0;
    }

    .deck-list-item {
        color: white;
        width: 100%;
        height: 40px;

        overflow-x: hidden;

        display: flex;
        justify-content: center;
        align-items: center;

        border-radius: 6px;
    }

    .deck-list-item:hover {
        background-color: #66626e;
        color: white;
    }

    .deck-edit-form {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
    }

    .deck-item-title {
        margin-left: 12px;
        overflow-x: hidden;
    }

    .edit-title {
        width: auto;
        color: black;
    }

    .deck-item-edit-btn {
        height: 20px;
        width: 30px;

        color: white;

        border: none;
        border-radius: 8px;
        background-color: inherit;
        margin-left: auto;
        margin-right: 10px;
        padding-bottom: 10px;

        font-size: 18px;

        display: flex;
        justify-content: center;
        align-items: center;
    }

    .deck-item-edit-btn:hover {
        background-color: #67404b;
    }

    .class-list-filter {
        display: flex;
        width: 100%;
        height: 50px;

        margin-top: auto;
    }

    .action-btn {
        min-width: 50px;
        width: fit-content;
        height: 25px;

        padding: 0 8px;
        background: none;
        border: 1px solid white;
        border-radius: 4px;
        color: white;
        box-shadow: none;

        font-family: crock, serif;
        font-size: 16px;
        cursor: pointer;
    }

    .action-btn:hover {
        background-color: #66626e;
    }

    .filter-btn {
        width: 150px;
        height: 40px;
        background: none;
        border: none;

        color: white;
        font-family: crock, serif;
        font-size: 18px;
        cursor: pointer;
    }

    .filter-btn:hover {
        background-color: #66626e;
        color: white;
    }

    .selected {
        background-image: url("/assets/ui/bgOutline.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 100% 100%;
    }

    .creator-action-container {
        display: flex;
        justify-content: flex-start;
        gap: 10px;
        padding: 0 8px 5px 8px;

        width: 100%;
        margin-top: 10px;

        border-bottom: 1px solid white;
    }

    .deck-info {
        margin-left: auto;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 10px;

        .deck-card-count, .deck-score-count {
            text-align: center;

            font-size: 16px;
            margin-left: auto;
            padding-top: 3px;
            margin-right: 10px;
        }
    }


    .creator-save-btn {
        margin-right: 16px;
    }

    .deck-rules {
        background-image: url("/assets/ui/bgPanel.png");
        background-size: 100% 100%;
        background-repeat: no-repeat;
        background-position: center;

        min-width: 250px;

        margin-top: auto;
        margin-left: 20px;
        padding: 18px;
        width: 400px;
        opacity: 0.8;

        border-radius: 8px;
    }
</style>
  