<script lang="ts">
    import {onDestroy, onMount} from 'svelte';
    import type {CardModel} from "src/models/card.model";
    import {cardPreview} from "src/store/cardPreview.store";
    import type {AbilityModel} from "src/models/ability.model";
    import {Config} from "src/store/config.store";

    let card: CardModel | null;

    let size: "sm" | "md" | "lg" = "lg";

    let previewB: boolean = false;

    let abilities: Array<AbilityModel> = [];

    const unSubscriber = cardPreview.subscribe((newCardToPreview) => {
        card = newCardToPreview;
    });

    $: {
        abilities = (card?.ability ?? []).map((ability) => {
            return $Config.abilities.find((abilityData) => abilityData.id === ability)!;
        });
    }

    const getCardType = () => {
        if (!card) {
            return "";
        }

        switch (card.type) {
            case 0:
                return "Close";
            case 1:
                return "Ranged";
            case 2:
                return "Siege";
            case 3:
                return "Leader";
            case 4:
                return "Special";
            case 5:
                return "Weather";
            default:
                return "";
        }
    }

    onDestroy(() => {
        unSubscriber();
    });


    onMount(() => {

    });
</script>

<main>
    {#if card}
        <div class="card-preview">
            <div class="preview-description" class:preview-b={previewB}>
                <p>{card.name}</p>
                {#each abilities as ability}
                    <p>{ability?.description}</p>
                {/each}
                <div class="card-deck-data">
                    {#if card.muster_type}
                        <span>Family: {card.muster_type}</span>
                    {/if}
                    <span>Type: {getCardType()}</span>
                    <span>Tier: {card.category === 0 ? "Bronze" : "Gold"}</span>
                    <span>Power: {card.power}</span>
                </div>
            </div>
            <img alt="{card.name}" class="previewed-card" src="assets/cards/{card.faction}/{card.img}.png"/>
        </div>
    {/if}
    <slot/>
</main>

<style>
    .card-preview {
        position: absolute;
        z-index: 99999997;
        top: 0;
        right: 0;
        margin: 32px;
        width: 400px;
        min-height: 300px;
        height: fit-content;
        display: flex;

        background-image: url("/assets/ui/bgPanel.png");
        background-size: 400px 100%;
        background-repeat: no-repeat;
        background-position: center;

        box-sizing: border-box;
        padding-left: 25px;
    }

    img {
        margin-left: auto;
    }

    .previewed-card {
        margin-left: auto;
        height: 295px;
    }

    .card-deck-data {
        display: flex;
        flex-direction: column;

        margin-top: auto;
        margin-bottom: 5px;
        /*margin-left: 10px;*/
    }

    .preview-description {
        display: flex;
        flex-direction: column;
        width: 100%;
        word-break: break-word;
        /*padding: 8px*/
    }
</style>
