<script lang="ts">
    import VisibilityProvider from './providers/VisibilityProvider.svelte';
    import {debugData} from './utils/debugData';
    import {useNuiEvent} from "./utils/useNuiEvent";
    import type {CardsData} from "./store/config.store";
    import {Config, currentRoute, currentRouteParams} from "./store/config.store";
    import Router from "./components/Router.svelte";
    import {Routes} from "./models/routes.model";
    import {CardModel} from "./models/card.model";
    import {AbilityModel} from "./models/ability.model";
    import NotificationProvider from "./providers/NotificationProvider.svelte";
    import {cards, catalog, leaderboard} from "./utils/fakeData";
    import CardPreviewProvider from "src/providers/CardPreviewProvider.svelte";
    import {inGame, room, roomFoeSide, roomSide, spectator} from "src/store/game.store";
    import {orderFactions} from "src/models/faction.model";

    debugData([
        {
            action: 'config',
            data: {
                config: {
                    cardData: cards,
                    abilityData: [],
                    factions: {
                        ambarino: "Neutral",
                        new_hanover: "Northern",
                        lemoyne: "Nilfgaardian",
                        new_austin: "Monsters",
                        west_elizabeth: "Scoia'tael"
                    }
                },
            },
        },
        {
            action: 'open',
            data: {
                route: Routes.cardDetails,
                params: {
                    // opponent: {
                    //     name: 'John Doe'
                    // },
                  cardKey: 'card_fontanatheatre',
                    // booster: {
                    //     id: 'booster_ambarino',
                    //     img: 'booster_ambarino.png'
                    // }
                },
            },
        },
        {
            action: 'response:catalog',
            data: {
                catalog: catalog,
            }
        },
        {
            action: 'response:leaderboard',
            data: {
                leaderboard: leaderboard,
            }
        }
    ]);

    useNuiEvent('config', (data) => {
        const {config} = data;
        if (!!config.cardData) {
            Config.set({
                ...$Config,
                cards: Object.values(config.cardData).map((card) => new CardModel().fromJson(card))
            } as CardsData);
        }
        if (!!config.abilityData) {
            Config.set({
                ...$Config,
                abilities: Object.values(config.abilityData).map((ability) => new AbilityModel().fromJson(ability)),
            } as CardsData);
        }
        if (!!config.factions) {
          console.log('adding factions')
            Config.set({
                ...$Config,
                factions: orderFactions(config.factions),
            } as CardsData);

          const allFaction = $Config.factions.find(f => f.id === 'all');

          console.log(allFaction)
          if (allFaction === undefined) {
            console.log('adding all faction')
            Config.set({
                ...$Config,
                factions: [
                    {
                        id: 'all',
                        label: 'All'
                    },
                    ...$Config.factions
                ],
            } as CardsData);
          }
        }
    });

    useNuiEvent('init:battle', (data) => {
        roomSide.set(data.side);
        roomFoeSide.set(data.foeSide);

        currentRouteParams.set({});
        currentRoute.set(Routes.battle);
    });

    useNuiEvent('response:joinRoom', (data) => {
        room.set(data.roomID);
        spectator.set(data.spectate);
        inGame.set(true);
    });
</script>

<main>
    <VisibilityProvider>
        <NotificationProvider>
            <CardPreviewProvider>
                <Router/>
            </CardPreviewProvider>
        </NotificationProvider>
    </VisibilityProvider>
</main>
