<script lang="ts">
    import {createEventDispatcher, onDestroy, onMount} from "svelte";
    import type {CardModel} from "src/models/card.model";
    import CardPreviewHandler from "src/utils/cardPreview";
    import {setAgile} from "src/store/game.store";
    import ContextMenu from "src/components/ContextMenu.svelte";
    import {sendNui} from "src/utils/fetchNui";

    export let card: CardModel;
    export let contextMenu: boolean = false;
    export let invalid: boolean = false;

    export let highlight: boolean = false;
    export let diff: boolean = false;
    export let qty: number | null = null;

    let active: boolean = false;
    let contextMenuOpen: boolean = false;

    const agileUnsubscriber = setAgile.subscribe((agile) => {
        active = !!agile && card.key === agile;
    });

    const hornUnsubscriber = setAgile.subscribe((horn) => {
        active = !!horn && card.key === horn;
    });

    const decoyUnsubscriber = setAgile.subscribe((decoy) => {
        active = !!decoy && card.key === decoy;
    });

    const onContextMenu = (isOpen: boolean) => {
      contextMenuOpen = isOpen;
    }

    const onShareCard = () => {
      sendNui("share:card", {key: card.key});
      contextMenuOpen = false;
    }

    const eventDispatcher = createEventDispatcher();

    function onClick() {
      if (contextMenuOpen) return;
      eventDispatcher("click", card);
    }

    let contextMenuItems = [
      {
        'name': 'Share',
        'onClick': onShareCard,
        'displayText': "Share Card",
        'class': 'fa-solid fa-plus'
      }
      // {
      //     'name': 'hr',
      // },
    ];

    onDestroy(() => {
        agileUnsubscriber();
        hornUnsubscriber();
        decoyUnsubscriber();
    });

    onMount(() => {
    });

</script>

<div class="card"
     class:active={active}
     class:disabled={card.disabled}
     class:highlight={highlight}
     class:invalid={invalid}
     on:click={onClick}
     on:focus={() => CardPreviewHandler.show(card)}
     on:keypress={onClick}
     on:mouseleave={() => CardPreviewHandler.hide()}
     on:mouseover={() => CardPreviewHandler.show(card)}
>
    <img alt="{card.name}" src="assets/cards/{card.faction}/{card.img}.png"/>
    {#if diff}
    <span class="card-attr-power" class:card-attr-positive={card.diffPos}>
        <span>{card.power}</span>
      </span>
    {/if}
    <i class="card-sm-{card.faction} card-sm-{card.faction}-{card.img}"></i>
    {#if qty}
        <div class="card-qty-holder">{qty}</div>
    {/if}
</div>

{#if contextMenu}
    <ContextMenu onOpenChange={onContextMenu} menuItems={contextMenuItems} />
{/if}

<style lang="scss">
  .card {
    position: relative;
    width: fit-content;
    height: 140px;

    cursor: pointer;
    user-select: none;
    transition: transform 0.2s ease-in-out;

    &.active {
      transform: scale(1.5, 1.5);
    }

    &.highlight {
      animation: WoobWoob 2s infinite;
    }
  }

  .card img {
    height: inherit;
    border-radius: 4px;
  }

  .card-qty-holder {
    position: absolute;
    bottom: 0;

    background-color: #3f4c5b;
    width: 20px;
    height: 20px;
    border-radius: 50%;

    text-align: center;
    font-size: 16px;
  }

  // ANIMATIONS
  @-webkit-keyframes WoobWoob {
    0% {
      box-shadow: 0 0 15px #ffbb0b;
    }
    50% {
      box-shadow: 0 0 40px #ffd135;
    }
    100% {
      box-shadow: 0 0 15px #ffbb0b;
    }
  }

  @-o-keyframes WoobWoob {
    0% {
      box-shadow: 0 0 15px #ffbb0b;
    }
    50% {
      box-shadow: 0 0 40px #ffd135;
    }
    100% {
      box-shadow: 0 0 15px #ffbb0b;
    }
  }

  @-ms-keyframes WoobWoob {
    0% {
      box-shadow: 0 0 15px #ffbb0b;
    }
    50% {
      box-shadow: 0 0 40px #ffd135;
    }
    100% {
      box-shadow: 0 0 15px #ffbb0b;
    }
  }

  @keyframes WoobWoob {
    0% {
      box-shadow: 0 0 15px #ffbb0b;
    }
    50% {
      box-shadow: 0 0 40px #ffd135;
    }
    100% {
      box-shadow: 0 0 15px #ffbb0b;
    }
  }
</style>
