<script lang="ts">
    import {useNuiEvent} from '../utils/useNuiEvent';
    import {sendNui} from '../utils/fetchNui';
    import {onMount} from 'svelte';
    import {catalogSearchMode, currentRoute, currentRouteParams, deckEditMode, visibility} from '../store/config.store';
    import reset from "src/utils/reset";
    import {inGame} from "src/store/game.store";

    let isVisible: boolean;

    visibility.subscribe((visible) => {
        isVisible = visible;
    });

    useNuiEvent('open', (data) => {
        visibility.set(true);

        if (data) {
            currentRoute.set(data.route);
            currentRouteParams.set(data.params);
        }
    });

    useNuiEvent('close', () => {
        console.log("closing...")
        visibility.set(false);
        currentRoute.set(null);
        reset();
    });

    onMount(() => {
        const keyHandler = (e: KeyboardEvent) => {
            if (isVisible && ['Escape', "Backspace"].includes(e.code)) {
              if (e.code === "Backspace" && ($deckEditMode || $catalogSearchMode)) {
                return;
              }
                if ($inGame) {
                    sendNui('request:leaveRoom');
                }
                //console.log('close')
                currentRoute.set(null);
                currentRouteParams.set(null);
                visibility.set(false);
                sendNui('close');
                reset();
            }
        };

        window.addEventListener('keydown', keyHandler);

        return () => window.removeEventListener('keydown', keyHandler);
    });
</script>

<main>
    {#if isVisible}
        <slot/>
    {/if}
</main>
