<script lang="ts">
  import type {CardModel} from "src/models/card.model";
  import {useNuiEvent} from "src/utils/useNuiEvent";
  import {catalogSearchMode, Config} from "src/store/config.store";
  import {sendNui} from "src/utils/fetchNui";
  import {onMount} from "svelte";
  import Card from "./Card.svelte";
  import {debugData} from "src/utils/debugData";
  import NotificationHandler from "src/utils/notification";
  import type {AllFactions, Faction} from "src/models/faction.model";

  let currentFaction: Faction = 'all';
    let isProcessing = false;

    let search: string = '';


    let inventory: Array<CardModel> = [];
    let catalog: Array<CardModel> = [];
    let currFactionCards: Array<CardModel> = [];

    useNuiEvent('response:catalog', (data) => {
        const responseCatalog = data.catalog;
        const catalogKeys = Object.keys(responseCatalog);
        catalog = $Config.cards.filter(card => catalogKeys.includes(card.key!) && card.key !== "none").map(card => {
            return {
                ...card,
                qty: responseCatalog[card.key!].qty
            } as CardModel
        });

        filterCardsToDisplay();
    });

    useNuiEvent('response:inventory', (inventoryData) => {
        const cardKeys = inventoryData.inventory.map(card => card.metadata.card);
        inventory = $Config.cards.filter(card => cardKeys.includes(card.key!) && card.key !== "none").map(card => {
            const inventoryCard = inventoryData.inventory.find(inventoryCard => inventoryCard.metadata.card === card.key);
            return {
                ...card,
                qty: inventoryCard?.count ?? 0
            } as CardModel
        });
    });

    useNuiEvent('confirmUpdate:catalog', (data) => {
        const {err, card, isRemove} = data;

        if (!!err) {
            NotificationHandler.error(err)
            return
        }

        if (isRemove) {
            const cardIdx = catalog.findIndex(c => c.key === card);
            if (cardIdx > -1) {
                addToInventory(cardIdx);
            }
        } else {
            const cardIdx = inventory.findIndex(c => c.key === card);
            if (cardIdx > -1) {
                addToCatalog(cardIdx);
            }
        }
    });

    const filterCardsToDisplay = () => {
      if (currentFaction === 'all') {
          currFactionCards = filterSearchCardList(sortCardList(catalog));
      } else {
        currFactionCards = filterSearchCardList(sortCardList(catalog.filter(c => c.faction === currentFaction)));
      }
    }

    const sortCardList = (cardList: CardModel[]) => {
        return cardList.sort((a, b) => {
            if (a.type !== b.type) {
                return b.type! - a.type!;
            } else {
                return a.key!.localeCompare(b.key!);
            }
        });
    }

  const filterSearchCardList = (cardList: CardModel[]) => {
      if (search === '') {
          return cardList;
      }
        return cardList.filter(c => c.name!.toLowerCase().includes(search));
  }

    const addToInventory = (cardIdx: number) => {
        const card = catalog[cardIdx];
        const invCardIdx = inventory.findIndex(c => c.key === card.key);

        // Add card to inventory
        if (invCardIdx > -1) {
            inventory[invCardIdx].qty++;
        } else {
            const copiedCard = JSON.parse(JSON.stringify(card));
            copiedCard.qty = 1;

            inventory.push(copiedCard);
        }

        //Remove card from catalog
        if (card.qty === 1) {
            catalog.splice(cardIdx, 1);
        } else {
            card.qty--;
        }

        inventory = inventory;
        catalog = catalog;
        filterCardsToDisplay();
        isProcessing = false;
    }

    const addToCatalog = (cardIdx: number) => {
        const card = inventory[cardIdx];
        const catalogCardIdx = catalog.findIndex(c => c.key === card.key);

        // Add card to catalog
        if (catalogCardIdx > -1) {
            catalog[catalogCardIdx].qty++;
        } else {
            const copiedCard = JSON.parse(JSON.stringify(card));
            copiedCard.qty = 1;

            catalog.push(copiedCard);
        }

        //Remove card from inventory
        if (card.qty === 1) {
            inventory.splice(cardIdx, 1);
        } else {
            card.qty--;
        }

        inventory = inventory;
        catalog = catalog;
        filterCardsToDisplay();
        isProcessing = false;
    }

    const onCardClick = (card: CardModel, section: 'inventory' | 'catalog') => {

        //e.preventDefault();
        //e.stopPropagation();

        if (section === 'catalog') {
            sendNui('update:catalog', {card: card.key, isRemove: true});
            debugData([
                {
                    action: "confirmUpdate:catalog",
                    data: {
                        err: null,
                        card: card.key!,
                        isRemove: true
                    }
                }
            ], 100)
            isProcessing = true;
            //app.send("update:catalog", {card: id, isRemove: true});
        } else if (section === 'inventory') {
            sendNui('update:catalog', {card: card.key, isRemove: false});
            debugData([
                {
                    action: "confirmUpdate:catalog",
                    data: {
                        err: null,
                        card: card.key!,
                        isRemove: false
                    }
                }
            ], 100)
            isProcessing = true;
            //app.send("update:catalog", {card: id, isRemove: false});
        }
        //render();
        filterCardsToDisplay();
    }

    const onFactionChange = (factionId: AllFactions) => {
        const faction = $Config.factions.find(f => f.id === factionId);

        if (!!faction) {
            currentFaction = factionId;
            filterCardsToDisplay();
        }
    }

    const onSearchFocus = (focus: boolean) => {
      catalogSearchMode.set(focus);
    }

    const onSearch = (e: KeyboardEvent) => {
      search = (e.target as HTMLInputElement).value.toLowerCase();

      filterCardsToDisplay();
    }

    onMount(() => {
    });

</script>

<div class="catalog-container">
    <div class="card-list-container">
        <div class="header">
            <h2 class="center">
                Inventory
            </h2>
        </div>
        <div class="faction-container">
        </div>
        <div class="card-list" data-id="inventory" id="inventory">
            {#each inventory as card}
                <Card
                      card={card}
                      diff={false}
                      qty={card.qty}
                      on:click={() => onCardClick(card, 'inventory')}
                      contextMenu={true}
                />
            {/each}
        </div>
    </div>
    <div class="card-list-container">
        <div class="header">
            <div class="empty"></div>
            <h2 class="center">
                Catalog
            </h2>
            <div class="search-bar">
                <input type="text" placeholder="Search..."
                       on:focusin={() => onSearchFocus(true)}
                       on:focusout={() => onSearchFocus(false)}
                       on:keyup={onSearch}
                />
            </div>
        </div>
        <div class="faction-container">
            <div class="class-list-filter">
                {#each $Config.factions as faction}
                    <button class="filter-btn" class:selected={faction.id === currentFaction}
                            on:click={() => onFactionChange(faction.id)}>{faction.label}</button>
                {/each}
            </div>
        </div>

        <div class="card-list catalog" id="catalog">
            {#each currFactionCards as card}
                <Card
                      card={card}
                      diff={false}
                      qty={card.qty}
                      on:click={() => onCardClick(card, 'catalog')}
                        contextMenu={true}
                />
            {/each}
        </div>
    </div>
</div>

<style lang="scss">
