        background-position: center;
        display: flex;
        justify-content: space-evenly;


        border-radius: 8px;
        padding: 64px;

        margin-right: 25%;
    }

    .card-list-container {
        width: 50%;
        height: 100%;

        display: flex;
        flex-direction: column;

        align-items: center;
    }

    .faction-container {
        width: 100%;
        min-height: 40px;
        /*flex: 1 1 auto;*/

        display: flex;
        flex-direction: column;

        align-items: flex-start;
        overflow-y: hidden;

    }

    .header {
        display: flex;
        flex: 0 1 auto;
        width: 100%;
        min-height: 32px;
        /*height: 30px;*/
        text-align: center;
        justify-content: center;
        align-items: center;
        margin: 0;

        .empty {
            flex: 1;
        }

          h2 {
            &.center {
              flex: 1;
            }
          }

        .search-bar {
          flex: 1;

          input {
            height: 30px;
            width: 200px;
          }
        }
    }

    .card-list {
        width: 95%;
        height: 95%;

        /*background-image: url("assets/ui/bgOutline2.png");*/
        /*background-size: 95% 95%;*/
        /*background-repeat: no-repeat;*/
        /*background-position: center;*/
        /*outline: white auto 1px;*/
        border-top: 1px solid white;
        /*border-radius: 6px;*/

        padding-top: 8px;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(75px, 1fr));
        grid-auto-rows: max-content;
        /*grid-template-rows: repeat(auto-fill, minmax(96px, 1fr));*/
        grid-gap: 10px;
        overflow-y: auto;
    }


    .class-list-filter {
        display: flex;
        flex: 0 1 auto;

        width: 100%;

        /*background-color: #1c1b1e;*/
    }

    .filter-btn {
        width: 140px;
        height: 40px;
        /*background-color: #49454e;*/
        background: none;
        border: none;

        color: white;
        font-family: crock, serif;
        font-size: 18px;
        /*border-right: 3px solid #1c1b1e;*/
    }

    .filter-btn:hover {
        background-color: #66626e;
        color: white;
    }

    .filter-btn.selected {
        /*background-color: white;*/
        /*color: black;*/
        background-image: url("/assets/ui/bgOutline.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 100% 100%;
    }
</style>
  