import {Serializable} from "./serializable.model";
import type {Faction} from "src/models/faction.model";
import type shinies from 'src/components/BigCard/shiny.json';

export type CardStyle = {
    id: string;
    name: string;
    set: string;
    rarity: string;
    supertype: string;
    subtypes: string[];
    types: string[];
    number: string;
    foil: string;
    mask: string;
};

export type Shiny = keyof typeof shinies;

export type ShinyList = {
    [key in Shiny]: CardStyle;
};
export interface Discard {
    cards: Array<CardModel>;
}

export type ImgType = 'card' | 'booster';

export type InventoryCard = {
    metadata: {
        card: string;
    }
    count: number;
}

class CardModel extends Serializable {
    constructor(
        public id?: number, // the instance id of the card during battle. undefined outside battle
        public key?: string, // the name id of the card
        public name?: string,
        public type?: number,
        public changedType?: number,
        public boost ?: number,
        public customBoost?: Array<string>,
        public power?: number,
        public forcedPower?: number,
        public muster_type?: string,
        public rarity?: string,
        public category?: number,
        public provision_cost?: number,
        public faction?: Faction,
        public img?: string,
        public qty?: number,
        public shiny?: Shiny,
        public ability: Array<string> = [],
        //public abilities: Array<AbilityModel> = [],
        public disabled: boolean = false,
        public diffPos: boolean = false,
    ) {
        super();
    }

    fromJson(json: any): this {
        super.fromJson(json);

        if (json["data"]) {

            const data = json["data"];

            this.key = data["key"];
            this.category = data["category"];
            this.faction = data["faction"];
            this.img = data["img"];
            this.name = data["name"];
            this.power = data["power"];
            this.provision_cost = data["provision_cost"];
            this.rarity = data["rarity"];
            this.type = data["type"];
            this.ability = data["ability"];
            this.muster_type = data["muster_type"];
            this.shiny = data["shiny"];
        }

        // this.abilities = json["abilities"]?.map((ability: any) => {
        //     return cardsData.abilities.find((a: any) => a.id === ability);
        // });

        // this.abilities = [
        //     new AbilityModel().fromJson({
        //         id: '1',
        //         name: 'Ability 1',
        //         description: 'Description 1',
        //     }),
        //     new AbilityModel().fromJson({
        //         id: '2',
        //         name: 'Ability 2',
        //         description: 'Description 2',
        //     })
        // ]

        return this;
    }
}

export {CardModel};