<script lang="ts">
    import {onMount} from "svelte";
    import {useNuiEvent} from "src/utils/useNuiEvent";
    import type {CardModel} from "src/models/card.model";
    import {Config} from "src/store/config.store";
    import type {BoosterData} from "src/models/booster.model";
    import CardProxy from "./BigCard/CardProxy.svelte";
    import {debugData} from "src/utils/debugData";
    import {sendNui} from "src/utils/fetchNui";
    import type {CardStyle} from "src/models/card.model";

    export let booster: BoosterData;

    let requestedOpening = false;
    let opened = false;
    let loot: CardModel[] = [];
    let boosterStyle: CardStyle = {
      "id": "swsh45sv-SV076",
      "set": "swsh45sv",
      "name": "Koffing",
      "supertype": "Pokémon",
      "subtypes": ["Basic"],
      "types": ["Darkness"],
      "number": "SV076",
      "rarity": "Rare Shiny",
    }

    useNuiEvent("response:booster", (data) => {
        loot = data.booster.map(bc => $Config.cards.find(c => c.key === bc))
        opened = true;
    });

    const onBoosterOpen = () => {
        if (requestedOpening) return;
        requestedOpening = true;

        sendNui('request:booster', {boosterId: booster.id});
        debugData([
            {
                action: "response:booster",
                data: {
                    booster: $Config.cards.slice(0, 5).map(c => c.key)
                }
            }
        ])
    }

    onMount(() => {
    });

</script>

<div class="booster-pack-container">
    {#if booster}
        {#if opened}
            <div class="card-loot-container" id="card-loot-container">
                {#each loot as card}
                    <CardProxy
                            id={boosterStyle.id}
                            faction={card.faction}
                            img="{card.img}"
                            name={boosterStyle.name}
                            number={boosterStyle.number || ""}
                            rarity={boosterStyle.rarity}
                            set={boosterStyle.set}
                            subtypes={boosterStyle.subtypes}
                            supertype={boosterStyle.supertype}
                            types={boosterStyle.types}
                            foil={boosterStyle.foil}
                            mask={boosterStyle.mask}
                            revealOnHover={true}
                            imgType="card"
                    />
                {/each}
            </div>
        {:else}
            <CardProxy
                    on:click={onBoosterOpen}
                    id={boosterStyle.id}
                    img="{booster.img}"
                    name={boosterStyle.name}
                    number={boosterStyle.number || ""}
                    rarity={boosterStyle.rarity}
                    set={boosterStyle.set}
                    subtypes={boosterStyle.subtypes}
                    supertype={boosterStyle.supertype}
                    types={boosterStyle.types}
                    foil={boosterStyle.foil}
                    mask={boosterStyle.mask}
                    imgType="booster"
                    height="{500}"
            />
        {/if}
    {/if}
</div>

<style>
    .booster-pack-container {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 1300px;
        height: 800px;

        border-radius: 8px;
    }

    .card-loot-container {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        gap: 32px;
    }
</style>
