<script lang="ts">
    import type {RouteParams} from "src/models/routes.model";
    import {Routes} from "src/models/routes.model";
    import {currentRoute, currentRouteParams} from "src/store/config.store";
    import Catalog from "./Catalog.svelte";
    import DeckBuilder from "./DeckBuilder.svelte";
    import Leaderboard from "./Leaderboard.svelte";
    import CardDetailed from "./CardDetailed.svelte";
    import Booster from "./Booster.svelte";
    import BattleBoard from "./Battle/BattleBoard.svelte";
    import OpponentSelection from "src/components/OpponentSelection.svelte";
    import ConfirmMatchmaking from "src/components/ConfirmMatchmaking.svelte";

    let currRoute: Routes | null = null;
    let currRouteParams: RouteParams | null = null;

    $: {
        currRoute = $currentRoute;
        currRouteParams = $currentRouteParams;
        console.log("updated route:", currRoute);
        console.log("updated params:", currRouteParams);
    }
</script>

<div class="container">
    {#if currRoute === Routes.booster}
        <Booster booster={currRouteParams?.booster}/>
    {:else if currRoute === Routes.cardDetails}
        <CardDetailed cardKey={currRouteParams?.cardKey}/>
    {:else if currRoute === Routes.catalog}
        <Catalog/>
    {:else if currRoute === Routes.leaderboard}
        <Leaderboard/>
    {:else if currRoute === Routes.deckBuilding}
        <DeckBuilder/>
    {:else if currRoute === Routes.opponentSelection}
        <OpponentSelection/>
    {:else if currRoute === Routes.confirmMatchmaking}
        <ConfirmMatchmaking opponent={currRouteParams?.opponent}/>
    {:else if currRoute === Routes.battle}
        <BattleBoard/>
    {/if}
</div>

<style>
    .container {
        position: absolute;
        /*width: 99vw;*/
        /*height: 99vh;*/
        width: 100%;
        height: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        overflow: hidden;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
</style>
