<script lang="ts">
    import {scale} from 'svelte/transition';
    import {onMount} from "svelte";
    import Card from "src/components/Card.svelte";
    import type {CardModel} from "src/models/card.model";
    import CardPreviewHandler from "src/utils/cardPreview";
    import {passing, setAgile, setHorn, waitForDecoy, waiting} from "src/store/game.store";
    import {sendNui} from "src/utils/fetchNui";

    type cardPosition = {
        // hue: number;
        rotation: number;
        offset: number;
    }

    export let cardList: Array<CardModel> = [];


    let totalCards = 0;
    let hoverIdx = -1;

    let activeCard: string | undefined = undefined;

    let cardStyles: Array<string> = [];

    $: {
        if ($setHorn) {
            activeCard = $setHorn;
        } else if ($setAgile) {
            activeCard = $setAgile;
        } else if ($waitForDecoy) {
            activeCard = $waitForDecoy;
        } else {
            activeCard = undefined;
        }

        totalCards = cardList.length;
        computeCardStyles();
    }

    const getCardStyleProperties = (index: number): cardPosition => {
        // const hue = (index / totalCards) * -360;
        const rotationRange = 50;
        const rotation = ((index - (totalCards - 1) / 2) / (totalCards - 2)) * rotationRange;
        const offsetRange = 80;
        const offset = Math.abs(((index - (totalCards - 1) / 2) / (totalCards - 2)) * offsetRange);

        return {
            rotation,
            offset
        }
    }

    const getCardStyle = (index: number) => {
        const {rotation, offset} = getCardStyleProperties(index);

        if (index !== hoverIdx) {
            return `
                transform: translateY(${offset}px) rotate(${rotation}deg);
            `;
        } else {
            return `
                transform: translateY(-100px) rotate(0deg) scale(2);
                transition-duration: 0ms;
                z-index: 5;
            `;
        }
    }

    const computeCardStyles = (cardIndex?: number) => {
        if (cardIndex !== undefined) {
            cardStyles[cardIndex] = getCardStyle(cardIndex);
            return;
        }

        cardStyles = cardList.map((card, index) => {
            return getCardStyle(index);
        });
    }

    const hoverCard = (card: CardModel | null, index: number) => {
        CardPreviewHandler.show(card);
        const oldIdx = hoverIdx;
        hoverIdx = index;

        computeCardStyles(hoverIdx);
        computeCardStyles(oldIdx);
    }

    const onCardClick = (card: CardModel) => {
        if ($waiting || $passing) return;

        if ($setAgile && card.key === $setAgile) {
            console.log("Cancel Agile", card.key)
            setAgile.set(undefined);
            sendNui('cancel:agile');
            return;
        }

        if ($setHorn && card.key === $setHorn) {
            console.log("Cancel Horn", card.key)
            setHorn.set(undefined);
            sendNui('cancel:horn');
            return;
        }

        if ($waitForDecoy && card.key === $waitForDecoy) {
            console.log("Cancel Decoy", card.key)
            waitForDecoy.set(undefined);
            sendNui('cancel:decoy');
            return;
        }

        console.log("Playing card from hand", card.key)
        sendNui('play:cardFromHand', {id: card.id});

        if (card.key === "decoy") {
            console.log("Waiting for decoy", card.key)
            waitForDecoy.set(card.key);
        }

    }

    onMount(() => {
        computeCardStyles();
    });
</script>

<div class="cards">
    {#each cardList as card, cardIndex}
        <div class="card"
             on:focus={() => hoverCard(card, cardIndex)}
             on:mouseover={() => hoverCard(card, cardIndex)}
             on:mouseleave={() => hoverCard(null, -1)}
             on:click={() => onCardClick(card)}
             on:keypress={() => onCardClick(card)}
        >
            <div
                    class="card-face"
                    style={cardStyles[cardIndex]}
                    transition:scale={{duration: 300}}
            >
                <Card card={card} highlight={card.key === activeCard}/>
            </div>
        </div>
    {/each}
</div>

<style lang="scss">
  .cards {
    position: absolute;
    bottom: 0;
    display: flex;
    height: 140px;
    padding: 0 50px;
    justify-content: center;
    left: 0;
    right: 0;
    user-select: none;
  }

  .card {
    height: 140px;
    margin: 0 -15px;
    position: relative;
    width: 75px;
    cursor: pointer;

    &:after {
      bottom: 0;
      content: '';
      left: -60px;
      position: absolute;
      right: -60px;
      top: 0;
      z-index: 10;
    }

    &:hover {
      .card-face:after {
        animation: fade 250ms ease-out forwards;
      }

      &:after {
        top: -175px;
      }
    }
  }

  .card-face {
    bottom: 0;
    content: '';
    left: 0;
    //pointer-events: none;
    position: absolute;
    right: 0;
    top: 0;
    transition: 800ms cubic-bezier(0.19, 1, 0.22, 1) transform;

    :global(.card) {
      border-radius: 16px;
      height: 100%;

    }

    &:after {
      animation: none;
      background: #fff;
      bottom: 0;
      content: '';
      left: 0;
      opacity: 0;
      position: absolute;
      right: 0;
      top: 0;
    }
  }

  @keyframes fade {
    0% {
      opacity: 0.9;
      transform: scale(1);
    }
    100% {
      opacity: 0;
      transform: scale(1.15);
    }
  }
</style>