<script lang="ts">
    import {createEventDispatcher} from "svelte";

    type Theme = "light" | "dark";
    export let title: string;

    export let theme: Theme = "light";

    const eventDispatcher = createEventDispatcher();

    function onClick(e) {
        eventDispatcher("click", e);
    }
</script>

<button class="cardgame-btn {theme}" on:click={onClick}>
    {title}
</button>

<style lang="scss">
  .cardgame-btn {
    min-width: 50px;
    height: 25px;

    padding: 0 8px;
    background: none;
    border: 1px solid white;
    border-radius: 4px;
    color: white;
    box-shadow: none;

    font-family: crock, serif;
    font-size: 16px;
    cursor: pointer;

    &.dark {
      border-color: #000;
      color: #000;
    }
  }

  .cardgame-btn:hover {
    background-color: #66626e;

    &.dark {
      background-color: #e6e6e6;
    }
  }
</style>
