import type {CardModel} from "src/models/card.model";
import {cardPreview} from "src/store/cardPreview.store";


export default class CardPreviewHandler {
    
    public static show = (card: CardModel | null) => {
        cardPreview.set(card);
    }

    public static hide = () => {
        cardPreview.set(null);
    }
}