import type {NotificationType} from "../store/notification.store";
import {notification} from "../store/notification.store";


export default class NotificationHandler {
    public static info = (message: string, title: string = "Info", duration: number = 5000) => NotificationHandler.message(title, message, "info", duration);

    public static success = (message: string, title: string = "Success", duration: number = 5000) => NotificationHandler.message(title, message, "success", duration);

    public static warning = (message: string, title: string = "Warning", duration: number = 5000) => NotificationHandler.message(title, message, "warning", duration);

    public static error = (message: string, title: string = "Error", duration: number = 5000) => NotificationHandler.message(title, message, "error", duration);

    private static message(title: string, message: string, type: NotificationType, duration: number) {
        console.log("Notification", title, message, type, duration)
        notification.set({
            title,
            message,
            type,
            duration,
        });
    }
}