<script lang="ts">
    import {useNuiEvent} from "src/utils/useNuiEvent";
    import type {PlayerUser} from "src/models/player.model";
    import {sendNui} from "src/utils/fetchNui";
    import {onMount} from "svelte";
    import {debugData} from "src/utils/debugData";

    let players: Array<PlayerUser> = [];

    useNuiEvent('select:players', data => {
        data.players.sort((a, b) => a.name.localeCompare(b.name));
        players = data.players;
    });

    const selectOpponent = (id: number) => {
        sendNui('request:askMatchmaking', {player: id});
    }

    const close = () => {
        sendNui('close');
    }

    onMount(() => {
        sendNui('request:matchmaking');


        // debug zone
        const p: PlayerUser = {
            name: 'Test',
            id: 1,
        }

        debugData([
            {
                action: 'select:players',
                data: {
                    players: [
                        p, p, p, p, p, p, p, p, p, p, p, p, p, p, p, p, p, p, p
                    ]
                }
            }
        ])
    });
</script>

<div id="character-selection">
    <button class="close-btn" on:click={close} on:keypress={close} type="button">X</button>
    <h1 class="title">Select an Opponent</h1>
    <ul id="character-list">
        {#each players as player}
            <li class="list-item" id="character-{player.id}" on:keypress={() => selectOpponent(player.id)}
                on:click={() => selectOpponent(player.id)}>{player.name}
                ({player.id})
            </li>
        {/each}
    </ul>
</div>

<style>
    .title {
        color: white;
        margin: 10px 0;
    }

    .close-btn {
        width: fit-content;
        text-align: end;
        margin-top: 5px;
        margin-left: auto;
        margin-right: 10px;
        font-size: 18px;
        font-weight: bold;
        background: none;
        border: none;
        color: white;
    }

    .close-btn:hover {
        cursor: pointer;
    }

    #character-selection {
        width: 400px;
        height: 400px;

        background-image: url("/assets/ui/bgPanel.png");
        background-size: 400px 400px;
        background-repeat: no-repeat;
        background-position: center;

        display: flex;
        flex-direction: column;
        align-items: center;
    }

    #character-list {
        width: 80%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        color: white;
        background-color: rgb(122, 121, 121);

        border-radius: 8px;

        font-size: 18px;
        line-height: 24px;
        list-style-type: none;
        padding: 0 0 0 10px;
        overflow: auto;
    }

    .list-item {
        width: 80%;
        padding: 10px;
        text-align: center;
        border-bottom: 1px solid white;
    }

    .list-item:last-child {
        border-bottom: none;
    }

    .list-item:hover {
        transition: background-color 0.3s;
        background-color: rgb(162, 159, 159);
        cursor: pointer;
    }
</style>
