<script lang="ts">
    import {sendNui} from "src/utils/fetchNui";
    import {onMount} from "svelte";
    import Button from "src/components/Button.svelte";

    export let opponent: {
        name: string,
    } = {
        name: 'Opponent',
    };

    const onSubmit = (res: 'accept' | 'refuse') => {
        sendNui('response:confirmMatchmaking', {res, opponent});
        sendNui('close');
    }

    const close = () => {
        onSubmit('refuse');
    }

    onMount(() => {
    });
</script>

<div id="recruit-confirmation">
    <button class="close-btn" on:click={close} type="button">X</button>
    <h1 class="title">Accept match ?</h1>
    <span class="subtitle">Match against <span id="opponent">{opponent.name}</span></span>

    <div class="recruit-choices-container">
        <Button on:click={() => onSubmit("refuse")} title="Refuse"/>
        <Button on:click={() => onSubmit("accept")} title="Accept"/>
    </div>
</div>

