import {CardModel} from "src/models/card.model";
import {Serializable} from "src/models/serializable.model";

export type SideFieldData = {
    cards: Array<CardModel>;
    score: number;
    horn?: CardModel;
}

export class SideField extends Serializable {
    constructor(
        public close: SideFieldData = {cards: [], score: 0},
        public ranged: SideFieldData = {cards: [], score: 0},
        public siege: SideFieldData = {cards: [], score: 0},
        public weather: SideFieldData = {cards: [], score: 0}
    ) {
        super();
    }

    fromJson(json: any): this {
        super.fromJson(json);

        if (json["close"]) {
            this.close.cards = this.close.cards.map((card: object) => new CardModel().fromJson(card));

            if (json["close"]["horn"]) {
                this.close.horn = new CardModel().fromJson(json["close"]["horn"]);
            }
        }

        if (json["ranged"]) {
            this.ranged.cards = this.ranged.cards.map((card: object) => new CardModel().fromJson(card));

            if (json["ranged"]["horn"]) {
                this.ranged.horn = new CardModel().fromJson(json["ranged"]["horn"]);
            }
        }

        if (json["siege"]) {
            this.siege.cards = this.siege.cards.map((card: object) => new CardModel().fromJson(card));

            if (json["siege"]["horn"]) {
                this.siege.horn = new CardModel().fromJson(json["siege"]["horn"]);
            }
        }

        if (json["weather"]) {
            this.weather.cards = this.weather.cards.map((card: object) => new CardModel().fromJson(card));
        }

        return this;
    }
}

export class SideInfoData extends Serializable {
    constructor(
        public lives: number = 0,
        public score: number = 0,
        public deck: number = 0,
        public hand: number = 0,
        public passing: boolean = false,
        public discard: Array<CardModel> = [],
        public name: string = "",
    ) {
        super();
    }

    fromJson(json: any): this {
        super.fromJson(json);

        if (json["discard"]) {
            const discard = typeof json["discard"] === "string" ? JSON.parse(json["discard"]) : json["discard"];
            this.discard = discard.map((card: object) => new CardModel().fromJson(card));
        }


        return this;
    }
}

export class SideInfo extends Serializable {
    constructor(
        public field: SideField = new SideField(),
        public info: SideInfoData = new SideInfoData(),
        public leader: CardModel = new CardModel(),
    ) {
        super();
    }

    fromJson(json: any): this {
        if (json["field"]) {
            this.field = new SideField().fromJson(json["field"]);
        }

        if (json["info"]) {
            this.info = new SideInfoData().fromJson(json["info"]);
        }

        if (json["leader"]) {
            this.leader = new CardModel().fromJson(json["leader"]);
        }

        return this;
    }
}