import {writable} from "svelte/store";
import type {RouteParams, Routes} from "../models/routes.model";
import type {CardModel} from "../models/card.model";
import type {AbilityModel} from "src/models/ability.model";
import type {FactionData} from "src/models/faction.model";

export interface CardsData {
    cards: Array<CardModel>;
    abilities: Array<AbilityModel>;
    factions: Array<FactionData>;
}

export const visibility = writable(false);
export const currentRoute = writable<Routes | null>(null);
export const currentRouteParams = writable<RouteParams | null>(null);

export const deckEditMode = writable(false);
export const catalogSearchMode = writable(false);

export const Config = writable({
    cards: [],
    abilities: [],
    factions: [],
} as CardsData);
