
export type Faction = "all" | "ambarino" | "new_hanover" | "lemoyne" | "west_elizabeth" | "new_austin";

export type AllFactions = Faction | "all";

export type FactionData = {
    id: AllFactions;
    label: string;
}; // store the Label for each faction. Loaded form config

export type FactionEvent = { [key in Faction]: string };

export function orderFactions(factions: FactionEvent): Array<FactionData> {
    const order: AllFactions[] = ["all", "ambarino", "new_hanover", "lemoyne", "west_elizabeth", "new_austin"];

    return order.map((faction) => ({
            id: faction,
            label: factions[faction],
        })
    ).filter((faction) => faction.label !== undefined);
}