import {isEnvBrowser} from "./misc";
import reset from "src/utils/reset";
import {inGame} from "src/store/game.store";

/**
 * @param eventName - The endpoint eventname to target
 * @param data - Data you wish to send in the NUI Callback
 *
 * @return returnData - A promise for the data sent back by the NuiCallbacks CB argument. in client, get the following params:
 * function handler(data, cb) end
 */
export async function fetchNui<T = any>(
    eventName: string,
    data: unknown = {}
): Promise<T> {
    if (isEnvBrowser()) return Promise.resolve(null as T);

    const options = {
        method: "post",
        headers: {
            "Content-Type": "application/json; charset=UTF-8",
        },
        body: JSON.stringify(data),
    };

    const resourceName = (window as any).GetParentResourceName
        ? (window as any).GetParentResourceName()
        : "cardgame";

    const resp = await fetch(`https://${resourceName}/${eventName}`, options);

    return await resp.json();
}


/**
 * @param eventName - The endpoint eventname to target
 * @param data - Data you wish to send in the NUI Callback
 */
export function sendNui<T = any>(
    eventName: string,
    data: unknown = {}
): void {
    if (isEnvBrowser()) return;

    if (eventName === "close") {
        reset();
    }

    console.log("sendNui", eventName)
    const options = {
        method: "post",
        headers: {
            "Content-Type": "application/json; charset=UTF-8",
        },
        body: JSON.stringify(data),
    };

    const resourceName = (window as any).GetParentResourceName
        ? (window as any).GetParentResourceName()
        : "cardgame";

    fetch(`https://${resourceName}/${eventName}`, options)
        .then(() => {
        })
        .catch((err) => console.error(err));
}
